<template>
    <main id="planCreate" class="container">
        <header class="head-title">{{headName}}</header>
        <el-form ref="planData" label-position="right" :model="planData" label-width="100px" :rules="rules" class="">
            <el-form-item label="赛事名称" prop="name" class="item">
                <el-input v-model="planData.name" placeholder="请填写赛事名称" required></el-input>
            </el-form-item>
            <el-form-item label="申请时间" prop="applyTime" class="item" v-if="$store.state.orgLevel===1">
                <el-date-picker
                        v-model="planData.applyTime"
                        type="daterange"
                        align="left"
                        :editable="false"
                        @change="formatTime1"
                        placeholder="申请时间范围"
                        :picker-options="pickerOptions">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="比赛时间" prop="gameTime" class="item">
                <el-date-picker
                        v-model="planData.gameTime"
                        type="daterange"
                        align="left"
                        :editable="false"
                        @change="formatTime2"
                        placeholder="比赛时间范围"
                        :picker-options="pickerOptions">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="赛事规模" prop="range">
                <el-select v-model="planData.range" placeholder="请选择">
                    <el-option
                            v-for="item in rangeOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="申请条件" prop="condition">
                <el-input type="textarea" v-model="planData.condition" placeholder="请详细描述赛事的申请条件" :maxlength="300" required></el-input>
            </el-form-item>
            <el-form-item label="保证金" prop="money" v-if="$store.state.orgLevel===1">
                <el-input-number v-model="planData.money" :min="0" placeholder="请输入保证金" required></el-input-number>
            </el-form-item>
            <div class="text-center">
                <el-button type="primary" @click="addPlan('planData')" :loading="isAjax" :disabled="isAjax">提 交</el-button>
                <el-button @click="resetForm('planData')">重 置</el-button>
            </div>
        </el-form>
    </main>
</template>
<script>
    export default{
        data(){
            const that = this;
            return {
                isAjax:false,
                editId:'',
                headName:'预发布赛事信息填写',
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() < Date.now() - 864e5;
                    },
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(yy,MM,dd,hh,mm,ss);
                            end.setTime(start.getTime() + 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(_time);
                            end.setTime(start.getTime() + 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(_time);
                            end.setTime(start.getTime() + 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                planData:{
                    name:'',
                    applyTime:'',
                    gameTime:'',
                    range:'',
                    condition:'',
                    money:''
                },
                rangeOptions:[
                    {value:58,label:'全国'},
                    {value:59,label:'省级'},
                    {value:61,label:'地市级'},
                    {value:60,label:'县市级'}
                ],
                rules:{
                    name:[
                        {required:true,message:'请输入赛事名称',trigger:'blur'},
                        {min:5,message:'赛事名称不低于5个字符',trigger:'blur'}
                    ],
                    applyTime:[
                        {type:'array',required:this.$store.state.orgLevel===1,message:'请选择比赛时间',trigger:'blur'}
                    ],
                    gameTime:[
                        {type:'array',required:true,message:'请选择比赛时间',trigger:'blur'}
                    ],
                    range:[
                        {type:'number',required:true,message:'请选择赛事规模',trigger:'change'}
                    ],
                    condition:[
                        {required:true,message:'请输入描述赛事申请条件',trigger:'blur'},
                        {min:15,message:'赛事申请条件长度不低于15个字符',trigger:'blur'}
                    ],
                    money:[
                        {type:'number',required:true,message:'请输入保证金',trigger:'blur'}
                    ]
                }
            }
        },
        methods:{
            resetForm(formName){
                this.$refs[formName].resetFields();
            },
            formatTime1(e){
                if( e !== '' && e !== null && typeof e !== 'undefined' && e !== undefined ){
                    this.planData.applyTime[0] = e.substring(0,10)+' 00:00:00';
                    this.planData.applyTime[1] = e.substring(13,24)+' 23:59:59';
                }
            },
            formatTime2(e){
                if( e !== '' && e !== null && typeof e !== 'undefined' && e !== undefined ){
                    this.planData.gameTime[0] = e.substring(0,10)+' 00:00:00';
                    this.planData.gameTime[1] = e.substring(13,24)+' 23:59:59';
                }
            },
            bossAdd(){
                const that = this;
                const __url = that.editId==='' ? that.$store.state.total.event.child.planAdd[1] : that.$store.state.total.event.child.planEdit[1];
                if(__url==='') return false;
                let par = {
                    title:that.planData.name,
                    scale:that.planData.range,
                    bail:that.planData.money,
                    condition:that.planData.condition,
                    game_start:that.planData.gameTime[0],
                    game_end:that.planData.gameTime[1],
                    apply_start:that.planData.applyTime[0],
                    apply_end:that.planData.applyTime[1]
                };
                let msg = '预发布赛事创建成功';
                if( that.editId !=='' ){
                    par.id = that.editId;
                    msg = '修改成功';
                }
                that.$ajax({
                    method:'post',
                    url:__url,
                    params:par
                }).then((res)=>{
                    that.isAjax = false;
                    if((res.data.errcode>>0)===0){
                        that.$message.success(msg);
                        that.$router.push('/Main/plan');
                    }else if((res.data.errcode>>0)===1000){
                        that.$parent.overTime();
                    }else{
                        this.$message.warning(res.data.msg);
                    }
                },(res)=>{
                    that.isAjax = false;
                    this.$message.warning('网络似乎出了点问题');
                });
            },
            orgAdd(){
                const that = this;
                const __url = that.editId==='' ? that.$store.state.total.event.child.myAdd[1] : that.$store.state.total.event.child.planEdit[1];
                if(__url==='') return false;
                let par = {
                    title:that.planData.name,
                    scale:that.planData.range,
                    condition:that.planData.condition,
                    game_start:that.planData.gameTime[0],
                    game_end:that.planData.gameTime[1],
                };
                let msg = '赛事申请成功';
                if( !isNaN(that.editId) ){
                    par.id = that.editId;
                    msg = '修改成功';
                }
                that.$ajax({
                    method:'post',
                    url:__url,
                    params:par
                }).then((res)=>{
                    that.isAjax = false;
                    if((res.data.errcode>>0)===0){
                        that.$message.success(msg);
                        that.$router.push('/Main/myEvent');
                    }else if((res.data.errcode>>0)===1000){
                        that.$parent.overTime();
                    }else{
                        this.$message.warning(res.data.msg);
                    }
                },(res)=>{
                    that.isAjax = false;
                    this.$message.warning('网络似乎出了点问题');
                });
            },
            addPlan(formName){
                const that = this;
                that.$refs[formName].validate((valid) => {
                    if(valid){
                        that.isAjax = true;
                        if(that.$store.state.orgLevel===1){
                            if(that.planData.applyTime[0] <= that.planData.gameTime[0]){
                                //省级发布计划
                                that.bossAdd();
                            }else{
                                that.isAjax = false;
                                that.$message.warning('申请开始时间不能晚于比赛开始时间');
                            }
                        }else{
                            //下级申请赛事
                            that.orgAdd();
                        }
                    }
                });
            }
        },
        mounted(){
            const that = this;
            const href = window.location.href;

            //下级进入该页面
            if(href.includes('eventApply')||href.includes('planEdit')){
                that.headName = '申请赛事';
            }
            // 判断编辑
            const index = href.indexOf('?');
            const id = parseInt(href.substring(index+1));
            // 赛事编辑 数据预读
            if( !isNaN(id) ){
                that.editId = id;
                that.headName = '编辑赛事信息';
                // 内存中存在数据 直接填充 (string 时 不存在  object 时存在)
                if(typeof that.$store.state.planEditData === 'string' || that.$store.state.planEditData.id !== id){
                    // 内存中数据不存在。获取数据  填充
                    const __url = that.$store.state.total.event.child.planDetail[1];
                    if(__url!==''){
                        that.$ajax({
                            url:__url,
                            params:{
                                id:that.editId
                            }
                        }).then(res=>{
                            const _data = res.data;
                            if((_data.errcode>>0)===0){
                                const dat = _data.data.eventinfo;
                                that.planData = {
                                    id        : id,
                                    name      : dat.title,
                                    applyTime : [dat.apply_start,dat.apply_end],
                                    gameTime  : [dat.game_start,dat.game_end],
                                    range     : dat.scale>>0,
                                    condition : dat.condition,
                                    money     : dat.bail
                                };
                                that.$store.commit('savePlanEditData',that.planData);
                            }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                                that.$parent.overTime();
                            }else{
                                that.$message.warning(_data.msg);
                            }
                        },res=>{
                            that.$message.warning('网络似乎出了点问题');
                        });
                    }
                }else if(typeof that.$store.state.planEditData === 'object' && that.$store.state.planEditData.id === id){
                    that.planData = that.$store.state.planEditData;
                }
            }else if(href.includes('planEdit')){
                that.editId = that.planData.id;
                that.planData = that.$store.state.planEditData;
                history.replaceState(null,'',location.hash + '?' + that.planData.id);
            }
        }
    }
</script>
<style>
    #planCreate .el-dialog__header .el-dialog__title,.inner-name,.inner-pwd{
        color:#20a0ff;
    }
    #planCreate .inner-name,.inner-pwd{
        margin-left:30px;
    }
    #planCreate .el-dialog__body p{
        margin-bottom:10px;
    }
    #planCreate .el-dialog__header{
        text-align:center;
    }
    #planCreate .el-dialog__footer{
        text-align:center;
    }
    #planCreate .inner-info{
        border:1px solid #ddd;
        padding:10px 8px;
    }
    #planCreate{
        background:#fff;
        padding-left:20px;
    }
    #planCreate .el-form{
        width:570px;
        margin-bottom:50px;
    }
    #planCreate .el-select{
        width:152px;
    }
</style>


