        weekNumber:['--','--','--'],
        sysInfo:[], // 通知系统消息
        opInfo:[], // 通知系统消息
        yearEvent:0, // 本年赛事
        monthEvent:0, // 本年赛事
        auth:'',
        orgId:'',
        userId:'',
        bindState:'',
        orgLevel:'',
