        pro:'',
        sysTime:'',
        printAuth:'',
        // 赛事编辑数据
        planEditData:'',
        planApplyData:'',
        // 机构编辑信息数据
        orgEditData:'',
        // 请求地址 （权限）
        uploadImg:'admin/Upload/uploadImg',
        getCashFee:'http://debug.api.yunbisai.com/request/Sign/GetRate?type=128',
        // 云赛事跳转
        getSSid:'admin/Getlogin/index',
        toYunbisai:'admin/Tologin/index',
        sendEmail:'sendcode/Sendmail/sendmail',
