<template>
    <main id="home" class="container">
        <section v-if="0">
            <header class="sysInfo">
                系统消息
                <label class="more" @click="more()">更多</label>
            </header>
            <main class="info-panel">
                <div class="info-detail"
                     v-for="info in $store.state.sysInfo"
                     @click=""
                     :key="info.id">
                    <div class="info-title">{{info.title}}</div>
                    <div class="info-time">{{info.time}}</div>
                </div>
            </main>
        </section>
        <div style="height:100px;"></div>
        <section class="statistics">
            <div class="memberS">
                <header>会员统计</header>
                <div>
                    <img src="../../assets/img/mem_home.png" alt="会员">
                    <div>
                        <p class="num"
                           :class="{memberS_go:$store.state.total.mem.auth}"
                           @click="goMem">
                            <span class="sm-num">进驻总数：</span>
                            {{$store.state.number[1] | showNum($store.state.number[1])}}
                        </p>
                        <p class="sm-num"
                           @click.stop="showDetail(1,'mem')"
                           :class="{memberS_go:$store.state.total.mem.auth}">
                            昨日新增：{{$store.state.yesterdayNumber[1] | showNum($store.state.yesterdayNumber[1])}}
                        </p>
                        <p class="sm-num"
                           @click.stop="showDetail(7,'mem')"
                           :class="{memberS_go:$store.state.total.mem.auth}">
                            近7日新增：{{$store.state.weekNumber[1] | showNum($store.state.weekNumber[1])}}
                        </p>
                    </div>
                </div>
            </div>
            <div class="organizationS">
                <header>机构统计</header>
                <div>
                    <img src="../../assets/img/org_home.png" alt="机构">
                    <div>
                        <p class="num"
                           @click="goOrg"
                           :class="{organizationS_go:$store.state.total.org.auth}">
                            <span class="sm-num">
                                进驻总数：</span>{{$store.state.number[0] | showNum($store.state.number[0])}}
                        </p>
                        <p class="sm-num" @click.stop="showDetail(7,'org')"
                           :class="{organizationS_go:$store.state.total.org.auth}">
                            近7日新增：{{$store.state.weekNumber[0] | showNum($store.state.weekNumber[0])}}
                        </p>
                    </div>
                </div>
            </div>
            <div class="eventS">
                <header>赛事统计</header>
                <div>
                    <img src="../../assets/img/event_home.png" alt="赛事">
                    <div>
                        <p class="num"
                           @click="goEve"
                           :class="{eventS_go:$store.state.total.event.auth}">
                            <span class="sm-num">总场数：</span>
                            {{$store.state.number[2] | showNum($store.state.number[2])}}
                        </p>
                        <p class="sm-num"
                        >
                            本年场数：{{$store.state.yearEvent | showNum($store.state.yearEvent)}}
                        </p>
                        <p class="sm-num">
                            近一月场数： {{$store.state.monthEvent | showNum($store.state.monthEvent)}}
                        </p>
                    </div>
                </div>
            </div>
        </section>
        <section v-if="0">
            <header class="opInfo">操作流程</header>
            <main class="info-panel">
                <div class="info-detail" v-for="info in opInfo">
                    <div class="info-title">{{info.title}}</div>
                    <div class="info-time">{{info.time}}</div>
                </div>
            </main>
        </section>
    </main>
</template>
<script>
    export default {
        data(){
            return {
                sysInfo:[
                    {title:'宁波市围棋协会申请了围棋协会的定级赛', time:'2017-6-20'},
                    {title:'温州市围棋协会申请了协会的定级赛', time:'2017-5-20'},
                    {title:'杭州市围棋协会上交100块', time:'2017-4-20'},
                    {title:'宁波市围棋协会赛', time:'2017-3-20'},
                    {title:'申请了围棋协会的定级赛', time:'2017-2-20'}
                ],
                opInfo:[
                    {title:'注册棋手操作流程', time:'2017-6-20'},
                    {title:'什么流程', time:'2017-5-20'},
                    {title:'申请赛事流程', time:'2017-4-20'},
                    {title:'操作规范', time:'2017-3-20'},
                    {title:'我都是随便写的', time:'2017-2-20'}
                ]
            }
        },
        filters:{
            showNum(value){
                if(typeof value !== 'number') return '--';
                return value.toString().split('').reverse().join('').replace(/(\d{3})/g,'$1,').replace(/\,$/,'').split('').reverse().join('');
            }
        },
        methods:{
            showDetail(time,type){
                const that = this;
                that.$store.commit('setSearchDay',time);
                switch (type) {
                    case 'org':
                        that.$parent.addTab('机构管理');
                        that.$router.push('/Main/org');
                        break;
                    case 'mem':
                        that.$parent.addTab('棋手管理');
                        that.$router.push('/Main/player');
                        break;
                }
            },
            more(){
               const root = this.$parent;
               root.addTab('消息');
               root.msgNum = 0;
               this.$router.push('/Main/more');
            },
            goOrg(){
                const that = this;
                const flag = that.$store.state.total.org.auth;
                if(flag){
                    that.$parent.addTab('机构管理');
                    that.$router.push('/Main/org');
                }
            },
            goEve(){
                const that = this;
                const flag = that.$store.state.total.event.auth;
                if(flag){
                    if(that.$store.state.orgLevel===1){
                        that.$parent.addTab('赛事计划');
                        that.$router.push('/Main/plan');
                    }else{
                        that.$parent.addTab('我的赛事');
                        that.$router.push('/Main/myEvent');
                    }
                }
            },
            goMem(){
                const that = this;
                const flag = that.$store.state.total.mem.auth;
                if(flag){
                    that.$parent.addTab('棋手管理');
                    that.$router.push('/Main/player');
                }
            }
        }
    }
</script>
<style>
    #home .sysInfo{
        border-left:6px solid #ff4949;
        margin-bottom:8px;
        padding-left:6px;
    }
    #home .opInfo{
        border-left:6px solid #f7ba2b;
        margin-bottom:8px;
        padding-left:6px;
    }
    #home .info-title{
        float:left;
    }
    #home .info-time{
        float:right;
        color:#99a9bf;
    }
    #home .info-panel{
        background:#fff;
        -webkit-box-shadow:0 1px 10px 0 #ddd;
        -moz-box-shadow:0 1px 10px 0 #ddd;
        box-shadow:0 1px 10px 0 #ddd;
    }
    #home .info-panel .info-detail{
        overflow:hidden;
        padding:10px;
        border-bottom:1px solid #E5E9F2;
        cursor:pointer;
    }
    #home .info-panel .info-detail:hover{
        background:#f7f7f7;
    }
    #home .info-panel .info-detail:last-child{
        border-bottom:none;
    }
    #home .statistics{
        overflow:hidden;
        margin:20px 0;
    }
    #home .organizationS_go,#home .memberS_go,#home .eventS_go{
        cursor:pointer;
    }
    #home .organizationS_go:hover,#home .memberS_go:hover,#home .eventS_go:hover{
        color:#666;
    }
    #home .memberS,.organizationS,.eventS{
        position:relative;
        font-size:18px;
        width:32%;
        height:206px;
        -webkit-box-shadow: 0 0 1px 1px #ddd;
        -moz-box-shadow: 0 0 1px 1px #ddd;
        box-shadow: 0 0 1px 1px #ddd;
        float:left;
        color:#fff;
    }
    #home .memberS>div,.organizationS>div,.eventS>div{
        font-size:14px;
        width:calc(100% - 40px);
        padding:0 20px;
        position: absolute;
        bottom:20px;
    }
    #home .memberS>div div,.organizationS>div div,.eventS>div div{
        position:absolute;
        bottom:0;
        right:20px;
    }
    #home .memberS>div>img{
        width:42%;
        max-width:90px;
    }
    #home .memberS>div div p,.organizationS>div div p,.eventS>div div p{
        text-align:right;
    }
    #home .memberS div,.organizationS div,.eventS div{
        float:right;
    }
    #home .memberS header,.organizationS header,.eventS header{
        margin:20px;
    }
    #home .memberS{
        background: #99a9c0;
    }
    #home .organizationS{
        background: #f7ba2b;
        margin:0 2%;
    }
    #home .eventS{
        background: #13ce67;
    }
    #home .eventMonth{
        border-left:8px solid #f7ba2b;
        margin-bottom:8px;
    }
    #home .more{
        color: #20a0ff;
        float:right;
        cursor:pointer;
        opacity:1;
    }
    #home .more:hover{
        color:#20a0ff;
        opacity:0.8;
    }
    #home .num{
        text-align:center;
        font-size:36px;
    }
    #home .sm-num{
        padding:4px 0;
        font-size:14px;
    }
</style>


