<template>
    <main id="myEvent" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入赛事名称">
                <el-button slot="append" icon="search" @click="search">查询</el-button>
            </el-input>
            <el-button class="add-new" type="success" v-if="$store.state.total.mem.child.memAdd" @click="addEvent">申请赛事</el-button>
            <div class="date-area">
                <span>比赛开始时间范围：</span>
                <el-date-picker
                        v-model="key.date"
                        type="daterange"
                        align="left"
                        placeholder="选择日期范围"
                        @change="formatTime"
                        :picker-options="pickerOptions">
                </el-date-picker>
            </div>
        </section>
        <section class="event-area">
            <el-tabs v-model="activeName" type="card" @tab-click="switchTab">
                <el-tab-pane class="plan" label="我的赛事" name="first">
                    <div class="text-center no-data" v-if="currentData_0.length===0">
                        <p>暂无赛事数据</p>
                    </div>
                    <div v-for="item in currentData_0" class="games">
                        <p class="title">
                            {{item.name}}
                            <span v-if="item.eid" style="color:#8492A6">（已绑赛事ID：{{item.eid}}）</span>
                            <el-tag v-if="item.isPlan===1" type="primary">省级</el-tag>
                            <el-tag v-if="item.isPlan!==1" type="warning">非省级</el-tag>
                            <el-tag v-if="item.state===0" type="primary">审核中</el-tag>
                            <el-tag v-if="item.state===2" type="success">已授权</el-tag>
                            <el-tag v-if="item.state===1" type="danger">未通过</el-tag>
                        </p>
                        <div class="game-info">
                            <i class="fa fa-building-o"></i><span>{{item.rank}}</span>
                            <i class="fa fa-cny text-red" v-if="!isNaN(item.money)"></i><span class="text-red" v-if="!isNaN(item.money)">保证金：{{item.money}} 元</span>
                            <el-button v-if="item.eid!==null" @click="getSSid(item.eid)" class="text-center btn-set" type="success" size="small">设置报名</el-button>
                            <el-button v-if="item.state===1" class="text-center btn-set" type="success" size="small" @click="watch(item.id)">查看详情</el-button>
                            <el-button v-if="item.eid===null&&item.state===2" class="text-center btn-set" type="success" size="small" @click="bindEvent(item.id)">绑定云比赛赛事</el-button>
                            <el-button class="btn-edit" type="primary" v-if="item.isPlan!==1&&item.state!==2" :disabled="item.state===1" size="small" @click="edit(item.id)">编 辑</el-button>
                            <el-button class="btn-del" type="danger"  v-if="item.isPlan!==1&&item.state!==2" size="small" @click="del(item.id,item.name)">删 除</el-button>
                        </div>
                        <div class="game-info">
                            <i class="fa fa-bullseye" v-if="item.applyTime[0]"></i><span v-if="item.applyTime[0]">申请时间：{{showTime(item.applyTime[0])+' 至 '+showTime(item.applyTime[1])}}</span>
                            <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i><span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                        </div>
                        <p class="intro">{{item.intro}}</p>
                    </div>
                    <div class="text-center pagination">
                        <el-pagination @size-change="handleSizeChange"
                                       @current-change="handleCurrentChange"
                                       :current-page="currentPage"
                                       :page-sizes="[6, 10, 20, 50]"
                                       :page-size="pageSize"
                                       layout="total,sizes, prev, pager, next, jumper"
                                       :total="totalNum">
                        </el-pagination>
                    </div>
                </el-tab-pane>
                <el-tab-pane class="plan" label="申请列表" name="second">
                    <div class="text-center no-data" v-if="currentData_1.length===0">
                        <p>暂无赛事数据</p>
                    </div>
                    <div v-for="item in currentData_1" class="games">
                        <p class="title">
                            {{item.name}}
                            <el-tag v-if="item.isPlan===1" type="primary">省级</el-tag>
                            <el-tag v-if="item.isPlan!==1" type="warning">非省级</el-tag>
                            <el-tag v-if="item.state===0" type="primary">审核中</el-tag>
                            <el-tag v-if="item.state===2" type="success">已授权</el-tag>
                            <el-tag v-if="item.state===1" type="danger">未通过</el-tag>
                        </p>
                        <div class="game-info">
                            <i class="fa fa-building-o"></i><span>{{item.rank}}</span>
                            <el-button class="text-center btn-edit" type="primary" size="small" @click="applyFor(item.id)">查看详情</el-button>
                        </div>
                        <div class="game-info">
                            <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i><span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                        </div>
                        <p class="intro">{{item.intro}}</p>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </section>
        <el-dialog class="bind-dialog" v-model="bindVis" title="绑定云比赛赛事">
            <div v-for="item in bindEventInfo.list" class="bind-div">
                <el-radio v-model="bindInfo.event_id" :key="item.event_id" :value="item.event_id" :label="item.event_id">{{item.title+' 赛事ID:'+item.event_id}}</el-radio>
            </div>
            <el-pagination
                    class="text-center"
                    small
                    @current-change="smallChangePage"
                    layout="prev, pager, next"
                    :total="bindEventInfo.totalNum">
            </el-pagination>
            <p class="tip">提示：请确保赛事正确绑定，完成绑定后可以使用完整的云比赛赛事功能。一旦绑定，无法修改。</p>
            <div class="text-center" style="margin-top:10px;">
                <el-button @click="bindVis = false" size="small">取 消</el-button>
                <el-button type="primary" @click="confirmBindEvent" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog size="tiny" title="删除赛事" v-model="delVis">
            <p>是否确认删除赛事 <span class="text-blue"> {{delName}}</span> ？</p>
            <div style="height:10px"></div>
            <p class="tip">提示：一旦删除无法恢复！</p>
            <div style="height:10px"></div>
            <div class="text-center">
                <el-button size="small" @click="delVis = false">取 消</el-button>
                <el-button type="primary" size="small" @click="confirmDel">确 认</el-button>
            </div>
        </el-dialog>
        <el-dialog title="绑定云比赛账号" v-model="bindYunVis">
            <p>
                暂未绑定云比赛账号，无法使用赛事服务。
            </p>
            <p class="tip" v-if="!$store.state.total.set.auth">提示：你的账号无此权限，请使用主管理员账号绑定。</p>
            <div style="height:10px"></div>
            <div class="text-center">
                <el-button size="small" @click="bindYunVis = false">取 消</el-button>
                <el-button type="primary" size="small" @click="goToBind" v-if="$store.state.total.set.auth">前往绑定</el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    export default{
        data(){
            const that = this;
            return {
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                key:{
                    keyword:'',
                    date:[],
                    type:'all',
                    url:this.$store.state.total.event.child.myList[1]
                },
                bindInfo:{
                    id:'',
                    event_id:''
                },
                bindEventInfo:{
                    list:[],
                    currentPage:1,
                    pageSize:5,
                    totalNum:0
                },
                editId:'',
                delVis:false,
                delName:'',
                bindYunVis:false,
                bindVis:false,
                activeName:'first',
                opText:'前往授权',
                pageSize:that.$parent.getCookie('myPlanPageSize') !== '' ? (that.$parent.getCookie('myPlanPageSize')>>0) : 6,
                currentPage:1,
                currentData_0:[],
                currentData_1:[],
                totalNum:0
            }
        },
        methods: {
            goToBind(){
                const that = this;
                if(!that.$store.state.total.set.auth) return false;
                that.$router.push('/Main/setting3');
                that.$parent.addTab('绑定云比赛');
            },
            edit(id){
                const that = this;
                that.$router.push('/Main/planEdit?'+id);
                that.$parent.addTab('编辑赛事');
            },
            del(id,name){
                const that = this;
                that.delName = name;
                that.editId = id;
                that.delVis = true;
            },
            confirmDel(){
                const that = this;
                const __url = that.$store.state.total.event.child.eventDel[1];
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.editId
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('删除成功');
                        that.delVis = false;
                        that.getData();
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                        that.delVis = false;
                    }
                },res=>{
                    that.delVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            watch(id){
                const that = this;
                that.$parent.addTab('申请赛事');
                that.$router.push('/Main/applyFor?'+id);
            },
            showTime(e){
                if( e!==null){
                    return e.substring(0,10);
                }else{
                    return false;
                }
            },
            formatTime(e){
                if( e !== '' ){
                    this.key.date[0] = e.substring(0,10)+' 00:00:00';
                    this.key.date[1] = e.substring(13,24)+' 23:59:59';
                }
                this.getData();
            },
            getRange(num){
                if(num===58) return '全国';
                if(num===59) return '省级';
                if(num===60) return '县市级';
                if(num===61) return '地市级';
            },
            handleSizeChange(pageSize){
                document.cookie = 'myPlanPageSize='+pageSize;
                const that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            smallChangePage(page){
                const that = this;
                that.bindEventInfo.currentPage = page;
                that.getBindList();
            },
            handleCurrentChange(page){
                const that = this;
                that.currentPage = page;
                that.getData();
            },
            addEvent(){
                const that = this;
                that.$parent.addTab('赛事申请');
                that.$router.push('/Main/eventApply');
            },
            applyFor(id){
                let that = this;
                this.$parent.addTab('申请赛事');
                that.$router.push('/Main/applyFor?'+id);
            },
            getBindList(){
                const that = this;
                const __url = that.$store.state.total.event.child.eventList[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        page:that.bindEventInfo.currentPage,
                        pageSize:that.bindEventInfo.pageSize
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.bindEventInfo.list = [];
                        dat.forEach( (e,i) => {
                            that.bindEventInfo.list.push({
                                event_id:e.event_id,
                                title:e.title
                            })
                        });
                        that.bindEventInfo.totalNum = _data.total;
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001 ){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            bindEvent(id){
                const that = this;
                that.bindInfo.id = id;
                that.bindVis = true;
                that.getBindList();
            },
            confirmBindEvent(){
                const that = this;
                const __url = that.$store.state.total.event.child.bindEvent[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.bindInfo.id,
                        event_id:that.bindInfo.event_id
                    }
                }).then(res=>{
                    that.bindVis = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$message.success('绑定成功');
                        that.getData();
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001 ){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            getData(){
                const that = this;
                if(that.key.url==='') return false;
                that.$ajax({
                    url:that.key.url,
                    params:{
                        page:that.currentPage,
                        pageSize:that.pageSize,
                        keywords:that.key.keyword,
                        start:that.key.date[0],
                        end:that.key.date[1]
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        if(that.key.url.includes('myList')){
                            that.currentData_0 = [];
                            dat.forEach((e,i)=>{
                                that.currentData_0.push({
                                    id:e.id,
                                    eid:e.event_id,
                                    name:e.title,
                                    rank:that.getRange(e.scale>>0),
                                    money:parseFloat(e.bail).toFixed(2),
                                    gameTime:[e.game_start,e.game_end],
                                    applyTime:[e.apply_start,e.apply_end],
                                    state:e.check_state>>0,
                                    intro:e.condition,
                                    org:(e.num === null) || (typeof e.num === 'undefined') ? 0 : e.num,
                                    isPlan:e.is_plan>>0
                                });
                            });
                            that.totalNum = _data.total;
                        }else{
                            that.currentData_1 = [];
                            dat.forEach((e,i)=>{
                                that.currentData_1.push({
                                    id:e.id,
                                    orgID:e.order_id,
                                    name:e.title,
                                    rank:that.getRange(e.scale>>0),
                                    gameTime:[e.game_start,e.game_end],
                                    intro:e.condition,
                                    org:e.num===null?0:e.num,
                                    payState:e.pay_state,
                                    state:(e.check_state>>0),
                                    isPlan:e.is_plan>>0
                                });
                            });
                        }
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else if( (_data.errcode>>0) === 1700 ){
                        that.bindYunVis = true;
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            switchTab(e){
                const that =this;
                if(e.index==='0'){
                    that.key.url = that.$store.state.total.event.child.myList[1];
                }else if(e.index==='1'){
                    that.key.url = that.$store.state.total.event.child.myPlanList[1];
                }
                that.getData();
            },
            search(){
                this.getData();
            },
            getSSid(eid){
                const that = this;
                const __url = that.$store.state.getSSid;
                let ssid;
                if( __url ==='' ) return false;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if( (_data.errcode>>0)===0 ){
                        ssid = _data.data.ssid;
                        that.toYunbisai(ssid,eid);
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            toYunbisai(ssid,eid){
                const that = this;
                const __url = that.$store.state.toYunbisai;
                if( __url ==='' ) return false;
                that.$parent.openNewTag();
                window.open(that.$store.state.baseURL + __url + '?ssid=' + ssid + '&event_id=' + eid);
            }
        },
        mounted(){
            const that = this;
            that.getData();
            if(window.location.href.includes('?pay')){
                that.$notify.success({
                    title:'支付成功',
                    message:'已成功申请赛事',
                    offset:80
                });
            }
            if(window.location.href.includes('?free')){
                that.$notify.success({
                    title:'申请成功',
                    message:'已成功申请赛事',
                    offset:80
                });
            }
        }
    }
</script>
<style>
    #myEvent .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #myEvent .tip{
        color:#8492A6;
        font-size:12px;
    }
    #myEvent .bind-dialog .bind-div{
        margin:0 0 8px 100px;
    }
    #myEvent .date-area{
        margin-top:20px;
        color:#8492A6;
        font-size:14px;
    }
    #myEvent .plan{
        width:100%;
    }
    #myEvent .text-red{
        color:#ff4949;
    }
    #myEvent .pagination{
        padding-bottom:20px;
    }
    #myEvent .games{
        padding:10px 30px;
        margin-bottom:10px;
    }
    #myEvent .games .intro{
        font-size:14px;
        color:#999;
        margin:10px 0 15px 0;
    }
    #myEvent .games .game-info{
        font-size:14px;
        margin:20px 0;
        position: relative;
    }
    #myEvent .games .game-info .fa{
        margin-right:8px;
    }
    #myEvent .games .game-info button{
        position: absolute;
    }
    /*#myEvent .games:hover .btn-edit, #myEvent .games:hover .btn-del, #myEvent .games:hover .btn-set{*/
        /*opacity: 1;*/
    /*}*/
    #myEvent .btn-set{
        top:-38px;
        right:18px;
    }
    #myEvent .btn-edit{
        top:0;
    }
    #myEvent .btn-del{
        top:40px;
    }
    #myEvent .btn-edit,#myEvent .btn-del{
        right:18px;
        /*-webkit-transition: all 0.3s ease-in;*/
        /*-moz-transition: all 0.3s ease-in;*/
        /*-ms-transition: all 0.3s ease-in;*/
        /*-o-transition: all 0.3s ease-in;*/
        /*transition: all 0.3s ease-in;*/
    }
    #myEvent .el-radio{
        white-space:normal;
    }
    #myEvent .games .game-info span{
        margin-right:30px;
    }
    #myEvent .games .game-info .el-button span{
        margin-right:0;
    }
    #myEvent .games .title{
        color:#20a0ff;
        margin:10px 0;
        text-align:left;
    }
    #myEvent .text-blue{
        color:#20a0ff;
        font-size:16px;
    }
    #myEvent .games .title .fa{
        font-size:22px;
        margin-left:8px;
    }
    #myEvent .el-icon-more {
        margin-left:14px;
    }
    #myEvent .search-area .add-new{
        margin-left:20px;
    }
    #myEvent .search-area .search-group{
        width:80%;
    }
    #myEvent .event-area{
        margin-top:20px;
        background:#fff;
    }
    #myEvent .games{
        border:1px solid #D3DCE6;
    }
    #myEvent .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        border-top:2px solid #20a0ff;
    }
    #myEvent .el-tabs__item{
        box-sizing:content-box;
    }
    #myEvent .el-tabs__nav-scroll{
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        background:transparent;
    }
    #myEvent .no-data{
        color:#bfcbd9;
    }
</style>


