<template>
    <main id="w_list" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入流水号或账户名">
                <el-select @change="search" style="width:100px;" v-model="key.type" slot="prepend" placeholder="全部">
                    <el-option label="全部" value="all"></el-option>
                    <el-option label="充值" value="1"></el-option>
                    <el-option label="转出" value="3"></el-option>
                    <el-option label="转入" value="4"></el-option>
                    <el-option label="转至云比赛" value="5"></el-option>
                    <el-option label="云比赛转入" value="6"></el-option>
                    <el-option label="提现" value="7"></el-option>
                </el-select>
                <el-button slot="append" icon="search" @click="search">查询</el-button>
            </el-input>
            <div class="date-area">
                <span>查询时间范围：</span>
                <el-date-picker
                        v-model="key.date"
                        type="daterange"
                        align="left"
                        placeholder="选择日期范围"
                        @change="formatTime"
                        :picker-options="pickerOptions">
                </el-date-picker>
                <span class="static">
                     总支出：
                    <span class="text-green text-large">{{outcome.toFixed(2)}} </span>
                    元；总收入：
                    <span class="text-red text-large">{{income.toFixed(2)}} </span>
                    元
                </span>

            </div>
        </section>
        <div class="refresh">
            <i class="fa fa-refresh" @click="getData"></i>
        </div>
        <el-table
                :data="currentData"
                stripe
                :loading="tableLoad"
                element-loading-text="拼命加载中"
                @expand="getDetail">
            <el-table-column type="expand">
                <template scope="scope">
                    <div class="detail-container" :loading="!scope.row.isAjax">
                        <el-form label-position="left" inline class="table-expand" v-if="scope.row.isAjax">
                            <el-form-item label="交易类型">
                                <span>{{ showDetailType(scope.row.type) }}</span>
                            </el-form-item>
                            <el-form-item label="交易时间">
                                <span>{{ scope.row.time }}</span>
                            </el-form-item>
                            <el-form-item label="对方账户名">
                                <span>{{ scope.row.opposite }}</span>
                            </el-form-item>
                            <el-form-item label="流水号">
                                <span>{{ scope.row.sn }}</span>
                            </el-form-item>
                            <el-form-item label="交易金额">
                                <span :class="{'text-red':scope.row.amount.includes('+'),'text-green':scope.row.amount.includes('-')}">{{scope.row.amount}} 元</span>
                            </el-form-item>
                            <el-form-item label="交易状态">
                                <span v-html="showState(scope.row.state)">{{ showState(scope.row.state) }}</span>
                            </el-form-item>
                            <el-form-item label="服务费" v-if="scope.row.type===7">
                                <span>{{ (scope.row.poundage-0).toFixed(2) || '0.00' }}</span>
                            </el-form-item>
                            <el-form-item label="备注">
                                <span>{{ scope.row.remark || '无' }}</span>
                            </el-form-item>
                        </el-form>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="index" label="编号" min-width="64">
            </el-table-column>
            <el-table-column prop="type" label="交易类型" min-width="92">
                <template scope="scope">
                    {{showType(scope.row.type)}}
                </template>
            </el-table-column>
            <el-table-column prop="time" label="交易时间" min-width="174">
            </el-table-column>
            <el-table-column prop="opposite" label="对方账户名" min-width="190">
            </el-table-column>
            <el-table-column prop="sn" label="流水号" min-width="220">
            </el-table-column>
            <el-table-column prop="amount" label="交易金额" min-width="120">
                <template scope="scope">
                    <span :class="{'text-red':scope.row.amount.includes('+'),'text-green':scope.row.amount.includes('-')}">{{scope.row.amount}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="rest" label="余额" min-width="120">
            </el-table-column>
        </el-table>
        <div class="text-center pagination">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[12, 20, 50, 100]"
                           :page-size="pageSize"
                           layout="total,sizes, prev, pager, next, jumper"
                           :total="totalNum">
            </el-pagination>
        </div>
    </main>
</template>
<script>
    export default {
        data(){
            const that = this;
            let yy,MM,dd,hh,mm,ss;
            const ttt = (d) => {
                return d.getFullYear()+'-'+(d.getMonth()+1)+'-'+(d.getDate())+' 23:59:59';
            };
            let _time = that.$store.state.sysTime,
                _nowTime = '',
                _oneMonthAgo = '';
            if(_time===''){
                _time = new Date();
                _nowTime = _time;
                _oneMonthAgo = new Date(_time.setMonth(_time.getMonth()-1));
                yy = _time.getFullYear();
                MM = _time.getMonth()+1;
                dd = _time.getDate();
                hh = _time.getHours();
                mm = _time.getMinutes();
                ss = _time.getSeconds();
            }else{
                yy = _time.substring(0,4);
                MM = _time.substring(5,7) - 1;
                dd = _time.substring(8,10);
                hh = _time.substring(11,13);
                mm = _time.substring(14,16);
                ss = _time.substring(17,19);
                _nowTime = new Date(yy,MM,dd,hh,mm,ss);
                _oneMonthAgo = new Date(yy,MM-1,dd,hh,mm,ss);
            }
            let hei = 0,win = window.outerWidth;
            if(win>1366){
                hei=600;
            }else if(win<=1366&&win>=1024){
                hei=450;
            }else{
                hei=300;
            }
            return {
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date(yy,MM,dd,hh,mm,ss);
                            const start = new Date(yy,MM,dd - 7,hh,mm,ss);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date(yy,MM,dd,hh,mm,ss);
                            const start = new Date(yy,MM - 1,dd,hh,mm,ss);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date(yy,MM,dd,hh,mm,ss);
                            const start = new Date(yy,MM - 3,dd,hh,mm,ss);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                tableLoad:true,
                key:{
                    keyword:'',
                    type:'',
                    date:[ttt(_oneMonthAgo),ttt(_nowTime)]
                },
                currentData:[],
                totalNum:0,
                currentPage:1,
                pageSize:12,
                tableHei:hei,
                outcome:0,
                income:0
            }
        },
        methods:{
            getDetail(e){
                // 只在展开时发送请求
                if(e.isExpand){
                    e.isExpand = !e.isExpand;
                    return false;
                }
                e.isExpand = !e.isExpand;
                // 提现才发请求，type===7
                if((e.type>>0)!==7) return false;
                const that = this;
                const __url = that.$store.state.total.wallet.child.cashDetail[1];
                if(__url === '') return false;
                that.$ajax({
                    url:__url,
                    method:'post',
                    params:{
                        id:e.sn
                    }
                }).then(res=>{
                    const _data = res.data;
                    if( (_data.errcode>>0) === 0 ){
                        e.isAjax = true;
                        e.poundage = _data.data.poundage;
                        e.state = _data.data.state;
                    }else if( (_data.errcode>>0) === 1000 || (_data.errcode>>0) === 1001 ){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                   that.$message.warning('网络似乎出了点问题');
                });
            },
            handleSizeChange(pageSize){
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            formatTime(e){
                if( e !== '' && e !== null && typeof e !== 'undefined' && e !== undefined ){
                    this.key.date[0] = e.substring(0,10)+' 00:00:00';
                    this.key.date[1] = e.substring(13,24)+' 23:59:59';
                    this.getData();
                }
            },
            showState(state){
               switch (state>>0){
                   case 0:return '<span class="text-orange">待处理</span>';
                   case 1:return '<span class="text-red">交易失败</span>';
                   case 2:return '<span class="text-green">已完成</span>';
               }
            },
            showDetailType(type){
                switch(type){
                    case 1:return '充值';
                    case 2:return '服务费';
                    case 3:return '转出';
                    case 4:return '转入';
                    case 5:return '转出云比赛';
                    case 6:return ':转入-来自云比赛';
                    case 7:return '提现';
                }
            },
            showType(type){
                switch(type){
                    case 1:return '充值';
                    case 2:return '服务费';
                    case 3:case 5:return '转出';
                    case 4:case 6:return '转入';
                    case 7:return '提现';
                }
            },
            search(){
                this.getData();
            },
            getData(){
                const that = this;
                const __url = that.$store.state.total.wallet.child.list[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        page:that.currentPage,
                        pageSize:that.pageSize,
                        keywords:that.key.keyword,
                        type:that.key.type,
                        begin_time:that.key.date[0],
                        end_time:that.key.date[1]
                    }
                }).then(res=>{
                    that.tableLoad = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.currentData = [];
                        dat.forEach((e,i)=>{
                            that.currentData.push({
                                index:i+1,
                                id:e.id,
                                type:e.type>>0,
                                state:2,
                                time:e.create_time.substring(0,19),
                                opposite:e.opposite,
                                sn:e.sn===null?'无':e.sn,
                                remark:e.remark,
                                amount:((e.state>>0)===1?'+':'-') + (e.money-0).toFixed(2),
                                poundage:null,
                                rest:(e.balance-0).toFixed(2),
                                isExpand:false,
                                isAjax:(e.type>>0) !== 7
                            });
                        });
                        that.totalNum = _data.total;
                        that.income = (_data.income === null ? 0 : _data.income);
                        that.outcome = (_data.outcome === null ? 0 : _data.outcome);
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.tableLoad = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            }
        },
        mounted(){
            this.getData();
        }
    }
</script>
<style>
    #w_list .search-area .search-group{
        width:60%;
    }
    #w_list .date-area{
        margin-top:20px;
        color:#8492A6;
        font-size:14px;
    }
    #w_list .pagination{
        padding-bottom:20px;
    }
    #w_list .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #w_list .refresh{
        display:inline-block;
        margin:10px 0;
        float:right;
    }
    #w_list .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #w_list .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #w_list .text-red{
        color:#ff4949;
    }
    #w_list .text-green{
        color:#13ce66;
    }
    #w_list .text-orange{
        color:#f7ba2a;
    }
    #w_list .text-large{
        font-size:18px;
    }
    #w_list .static{
        float:right;
        margin-right:40px;
    }
    #w_list .table-expand {
        font-size: 0;
    }
    #w_list .table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    #w_list .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 33%;
    }
    #w_list .detail-container{
        min-height:100px;
    }
</style>


