<template>
    <main id="orgList" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key" placeholder="请输入机构名称">
                <el-select style="width:120px;" v-model="city" slot="prepend" placeholder="全部" @change="search">
                    <el-option value="" label="全部区域"></el-option>
                    <el-option v-for="item in cityArr" :key="item[0]" :value="item[0]" :label="item[1]"></el-option>
                </el-select>
                <el-button slot="append" icon="search" @click="search" :disabled="isSearch" :loading="isSearch">查询</el-button>
            </el-input>
            <el-button class="add-new" type="success" v-if="$store.state.total.org.child.orgAdd[1]" @click="addNewOrg($event)">添加机构</el-button>
            <el-radio-group v-model="level" @change="search">
                <el-radio :label="0" v-if="$store.state.orgLevel===1||$store.state.orgLevel===2">全部机构</el-radio>
                <el-radio :label="2" v-if="$store.state.orgLevel===1">二级机构</el-radio>
                <el-radio :label="3" v-if="$store.state.orgLevel===1||$store.state.orgLevel===2">三级机构</el-radio>
                <el-radio :label="4"  v-if="$store.state.orgLevel===1||$store.state.orgLevel===2">四级机构</el-radio>
            </el-radio-group>
            <el-button size="small" type="info" class="reset" @click="reset">重置搜索条件</el-button>
        </section>
        <div class="el-bread-container">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item @click.native="breadClick(1)">全部</el-breadcrumb-item>
                <el-breadcrumb-item @click.native="breadClick(0)" v-show="breadPath[0].name !==''">{{breadPath[0].name}}</el-breadcrumb-item>
                <el-breadcrumb-item v-show="breadPath[1].name !==''">{{breadPath[1].name}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="refresh">
            <i class="fa fa-refresh" @click="search"></i>
        </div>
        <section class="player-list">
            <el-table
                    :data="currentData"
                    stripe
                    :loading="$store.state.tableLoad"
                    element-loading-text="拼命加载中"
                    empty-text="暂无相应机构"
                    @cell-click="showChildren">
                <el-table-column min-width="240" label="基本信息">
                    <template scope="scope">
                        <div class="org-name">
                            <p>机构名称：{{scope.row.org}}</p>
                            <p>类型：{{scope.row.type}}</p>
                            <p>上属机构：{{scope.row.upper}}</p>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column min-width="160" label="账号信息" >
                    <template scope="scope">
                        <p>联系人：{{scope.row.linkMan}}</p>
                        <p>联系电话：{{scope.row.linkMob}}</p>
                        <p>已绑云比赛账号：{{scope.row.bindAccount}}</p>
                    </template>
                </el-table-column>
                <el-table-column min-width="130" label="机构信息">
                    <template scope="scope">
                        <p>会员数：{{scope.row.childMem}}</p>
                        <p>下属机构数：{{scope.row.childOrg}}</p>
                        <p>昨日新增会员数：{{scope.row.yes_childMem}}</p>
                        <p>昨日新增下属机构数：{{scope.row.yes_childOrg}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="操作" min-width="60">
                    <template scope="scope">
                        <el-button type="text" v-if="$store.state.total.org.child.orgDetail[1]" @click="watch(scope.row.id)">详情</el-button>
                        <br>
                        <!--<el-popover placement="bottom" width="80">-->
                            <!--<el-button size="mini" type="text" v-if="$store.state.total.org.child.orgEdit[1]" @click="edit(scope.row.id)">修改</el-button>-->
                            <el-button class="text-red" size="mini" v-if="$store.state.total.org.child.orgDel[1]" type="text" @click="del(scope.row.id,scope.row.org)">注销</el-button>
                            <!--<el-button type="text" slot="reference">-->
                                <!--<i class="el-icon-more watch"></i>-->
                            <!--</el-button>-->
                        <!--</el-popover>-->
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[20, 50, 100]"
                           :page-size="pageSize"
                           layout="total,sizes, prev, pager, next, jumper"
                           :total="totalNum">
            </el-pagination>
        </section>
        <el-dialog title="注销机构" :visible.sync="delVis" size="tiny">
            是否确认注销 <span class="del-name">{{delName}}</span> ？
            <div style="height:10px"></div>
            <el-form label-width="54px">
                <el-form-item label="密码：">
                    <el-input size="small" placeholder="请输入密码以确认" auto-complete="new-password" type="password" v-model="delPwd"></el-input>
                </el-form-item>
            </el-form>
            <div style="height:10px"></div>
            <p class="tip">提示：一旦注销，该账号将不可用。请慎重操作！</p>
            <div class="text-center" style="margin-top:10px">
                <el-button size="small" @click="delVis = false">取 消</el-button>
                <el-button size="small" type="primary" @click="confirmDel">确 认</el-button>
            </div>
        </el-dialog>
        <el-dialog title="机构详情" :visible.sync="dialogVisible">
            <div class="dialog-inner"><span>机构名称：{{orgDetail.name}}</span></div>
            <div class="dialog-inner"><span>机构类型：{{orgDetail.type}}</span></div>
            <div class="dialog-inner"><span>上属机构：{{orgDetail.parent}}</span></div>
            <div class="dialog-inner"><span>法人姓名：{{orgDetail.person}}</span></div>
            <div class="dialog-inner"><span>联系人姓名：{{orgDetail.linkMan}}</span></div>
            <div class="dialog-inner"><span>联系电话：{{orgDetail.linkMob}}</span></div>
            <div class="dialog-inner"><span>地址：{{orgDetail.address}}</span></div>
            <div class="dialog-inner"><span>机构会员数：{{orgDetail.memNum}}</span></div>
            <div class="dialog-inner"><span>下属机构数：{{orgDetail.childNum}}</span></div>
            <div class="dialog-inner"><span></span></div>
            <div class="dialog-inner dialog-img"><label>租赁合同：</label><img v-if="orgDetail.contract.length" v-for="item in orgDetail.contract" :src="$store.state.imgURL+item" alt="合同图"><label v-if="!orgDetail.contract.length">暂无</label></div>
            <div class="dialog-inner dialog-img"><label>棋场照片：</label><img v-if="orgDetail.field.length" v-for="item in orgDetail.field" :src="$store.state.imgURL+item" alt="场地图"><label v-if="!orgDetail.field.length">暂无</label></div>
            <div class="dialog-inner dialog-img"><label>营业执照：</label><img v-if="orgDetail.license.length" v-for="item in orgDetail.license" :src="$store.state.imgURL+item" alt="执照图"><label v-if="!orgDetail.license.length">暂无</label></div>
            <div class="dialog-inner dialog-img"><label></label></div>
            <section style="clear:both;height:10px"></section>
            <footer class="btm text-center">
                <el-button type="primary" size="small" @click="edit" v-if="$store.state.total.org.child.orgEdit[1]">修改</el-button>
            </footer>
        </el-dialog>
    </main>
</template>
<script>
    export default{
        data(){
            const that = this;
            let hei = 0,win = window.outerWidth;
            if(win>1366){
                hei=640;
            }else if(win<=1366&&win>=1024){
                hei=450;
            }else{
                hei=300;
            }
            return {
                isSearch:false,
                thisId:'',
                delPwd:'',
                delId:'',
                delName:'',
                delVis:false,
                dialogVisible:false,
                key:'',
                city:'',
                pid:'',
                level:0,
                currentPage:1,
                pageSize:that.$parent.getCookie('orgPageSize') !== '' ? (that.$parent.getCookie('orgPageSize')>>0) : 20,
                totalNum:0,
                tableHei: hei,
                currentData:[],
                breadPath:[
                    {name:'',id:''},
                    {name:'',id:''},
                ],
                cityArr:[],
                orgDetail:{
                    name:'',
                    type:'',
                    parent:'',
                    person:'',
                    linkMan:'',
                    linkMob:'',
                    address:'',
                    memNum:'',
                    childNum:'',
                    contract:[],
                    field:[],
                    license:[],
                },
                flag:true
            }
        },filters:{
            showNum(value){
                return value.toString().split('').reverse().join('').replace(/(\d{3})/g,'$1,').replace(/\,$/,'').split('').reverse().join('');
            }
        },
        methods:{
            reset(){
                let onlyKey = this.key !== '' && this.level === 0 && this.currentPage === 1 && this.pid === '' && this.breadPath[0].id === '' && this.breadPath[1].id === '' && this.city === '';
                this.key = '';
                this.level = 0;
                this.currentPage = 1;
                this.pid = '';
                this.breadPath = [
                    {name:'',id:''},
                    {name:'',id:''},
                ];
                this.city = '';
                if(onlyKey) this.getData();
            },
            breadClick(type){
                const that = this;
                switch (type){
                    case 0:
                        // type ： 2
                        that.pid = that.breadPath[0].id;
                        that.currentPage = 1;
                        that.getData();
                        that.breadPath[1] = {name:'',id:''};
                        break;
                    case 1:
                        // all
                        that.breadPath = [
                            {name:'',id:''},
                            {name:'',id:''},
                        ];
                        that.pid = '';
                        that.level = 0;
                        that.currentPage = 1;
                        that.getData();
                        break;
                }
            },
            showChildren(row,column,cell){
                const getType = (type) => {
                    if(type.includes('二')) return 0;
                    if(type.includes('三')) return 1;
                };
                const that = this;
                if( cell.cellIndex === 0 ){
                    const _index = getType(row.type);
                    if(!row.type.includes('四') && that.flag){
                        that.breadPath[_index].id = row.id;
                        that.breadPath[_index].name = row.org;
                        that.pid = row.id;
                        that.currentPage = 1;
                        that.level = ( that.getTypeVal(row.type) + 1 );
                        that.getData();
                    }
                }
            },
            watch(id){
                const that = this;
                const root = that.$parent;
                const __url = that.$store.state.total.org.child.orgDetail[1];
                if(__url==='') return false;
                that.thisId = id;
                that.edit();
            },
            edit(){
                let that = this;
                that.$parent.addTab('机构信息');
                that.$router.push('/Main/OrgEdit?'+that.thisId);
            },
            del(id,name){
                this.delId = id;
                this.delName = name;
                this.delVis = true;
            },
            confirmDel(){
                const that = this;
                const __url = that.$store.state.total.org.child.orgDel[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    method:'post',
                    params:{
                        orgID:that.delId,
                        pwd:that.delPwd
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$message.success(_data.msg);
                        that.delVis = false;
                        that.getData();
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                        that.delVis = false;
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            getType(num){
                if(num==='1') return '一级（省级）';
                else if(num==='2') return '二级（地市级）';
                else if(num==='3') return '三级（县区级）';
                else if(num==='4') return '四级（培训学校）';
            },
            getTypeVal(num){
                if(num==='一级（省级）') return 1;
                else if(num==='二级（地市级）') return 2;
                else if(num==='三级（县区级）') return 3;
                else if(num==='四级（培训学校）') return 4;
            },
            search(){
                const that = this;
                that.currentPage = 1;
                that.getData();
            },
            handleSizeChange(pageSize){
                document.cookie = 'orgPageSize='+pageSize;
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            getData(){
                const that = this;
                const __url = that.$store.state.total.org.child.orgList[1];
                if(__url==='' || !that.flag ) return false;
                that.flag = false;
                that.tableLoad = true;
                // params
                let par = {
                    keyword  : that.key,
                    city     : that.city,
                    pageSize : that.pageSize,
                    page     : that.currentPage,
                    p_id     : that.pid,
                    level    : that.level
                };

                if (that.$store.state.searchDay) {
                    try{
                        par.end_time = that.$store.state.sysTime.substring(0,10) + ' 00:00:00';
                    }catch(error){
                        //par.end_time = new Date();
                    }
                    const _now = new Date(par.end_time);
                    const _before = new Date( _now.setDate( _now.getDate() - that.$store.state.searchDay ) );
                    par.start_time = _before.getFullYear() + '-' + ( _before.getMonth() + 1 ) + '-' + ( _before.getDate() < 10 ? '0' + _before.getDate() : _before.getDate() ) + ' 00:00:00';
                    that.$store.commit('setSearchDay',0);
                }

                that.$ajax({
                    url:__url,
                    method:'get',
                    params:par
                }).then(res=>{
                    let _data = res.data;
                    that.$store.state.tableLoad = false;
                    that.flag = true;
                    if((_data.errcode>>0)===0){
                        that.currentData=[];
                        if(_data.data!==null){
                            const dat = _data.data;
                            dat.forEach((e,i)=>{
                                that.currentData.push({
                                    id:dat[i].id,
                                    org:dat[i].org_name,
                                    type:that.getType(dat[i].org_level),
                                    person:dat[i].legal_name === null || dat[i].legal_name === '' ? '未填写' : dat[i].legal_name,
                                    linkMan:dat[i].link_name === null || dat[i].link_name === '' ? '未填写' : dat[i].link_name,
                                    linkMob:dat[i].link_phone === null || dat[i].link_phone.replace(/\s/g,'') === '' ? '未填写' : dat[i].link_phone,
                                    upper:dat[i].parent_name ? dat[i].parent_name : '无',
                                    childOrg:dat[i].org_num === null ? '--' : dat[i].org_num,
                                    yes_childOrg:dat[i].add_org_num === null ? '--' : dat[i].add_org_num,
                                    childMem:dat[i].member_num === null ? '--' : dat[i].member_num,
                                    yes_childMem:dat[i].add_member_num === null ? '--' : dat[i].add_member_num,
                                    bindAccount:dat[i].cname === null ? '未绑定' : dat[i].cname,
                                });
                            });
                            that.totalNum = (_data.total>>0);
                        }
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message(_data.msg);
                        that.$router.push('/login');
                    }
                },res=>{
                    that.$store.state.tableLoad = false;
                    that.$message({message:'网络似乎出了点问题',type:'warning'});
                });
            },
            addNewOrg(){
                let root = this.$parent;
                root.addTab('添加机构',root);
                this.$router.push('/Main/OrgAdd');
            }
        },
        mounted(){
            const that = this;

            that.getData();
            try{
                that.cityArr = that.$parent.cityQuery(that.$store.state.pro,'province','linkage');
                that.cityArr.forEach( (e,i) => {
                    if(e[0].includes(',')){
                        that.cityArr[i][0] = e[0].substring(0,e[0].indexOf(','));
                    }
                });
            }catch(error){

            }
        }
    }
</script>
<style>
    #orgList .el-dialog{
        padding-bottom:20px;
    }
    #orgList .tip{
        color:#8492A6;
        font-size:12px;
    }
    #orgList .del-name{
        font-size:18px;
        color:#20a0ff;
    }
    #orgList .dialog-inner{
        float:left;
        width:calc( 50% - 3px );
        padding:10px 0;
        border-top:1px solid #ddd;
        border-left:1px solid #ddd;
        min-height:20px;
    }
    #orgList .dialog-inner.dialog-img{
        height:125px;
        line-height:125px;
        overflow:hidden;
    }
    #orgList .dialog-inner.dialog-img span,.dialog-inner.dialog-img label{
        float:left;
    }
    #orgList .dialog-inner:nth-of-type(2n){
        border-right:1px solid #ddd;
    }
    #orgList .dialog-inner:nth-last-of-type(1),.dialog-inner:nth-last-of-type(2){
        border-bottom:1px solid #ddd;
    }
    #orgList .dialog-inner span{
        margin-left:20px;
        display:block;
        width:290px;
        -ms-text-overflow:ellipsis;
        text-overflow:ellipsis;
        overflow:hidden;
        white-space:nowrap;
    }
    #orgList .dialog-inner img{
        height:100px;
    }
    #orgList .el-radio-group{
        margin-top: 10px;
        display:inline-block;
    }
    #orgList .reset{
        margin:12px 0 0 40px;
    }
    #orgList a:hover{
        text-decoration: none;
    }
    #orgList .el-button--text.text-red span{
        color:#ff4949;
    }
    #orgList .org-name{
        cursor:pointer;
        display:block;
        width:100%;
    }
    #orgList .el-button--text.text-red:hover span{
        opacity:0.7;
    }
    #orgList .popper__arrow{
        left:43px !important;
    }
    #orgList .el-popover{
        min-width:70px;
        width:70px !important;
    }
    #orgList .watch{
        padding:0;
        color: #20a0ff;
        cursor:pointer;
        opacity:1;
    }
    #orgList .watch:hover{
        color:#20a0ff;
        opacity:0.8;
    }
    #orgList .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #orgList .el-icon-more {
        margin-left:14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius:0;
    }*/
    #orgList .search-area .add-new{
        margin-left:20px;
    }
    #orgList .el-bread-container{
        float:left;
    }
    #orgList .el-bread-container .el-breadcrumb{
        line-height:40px;
    }
    #orgList .search-area .search-group{
        width:80%;
    }
    #orgList .player-list{
        margin-top:20px;
        padding:0;
    }
    #orgList .player-list .player-num{
        color:#13ce66;
        font-size:18px;
    }
    #orgList .el-pagination{
        margin-top:10px;
        text-align:center;
    }
    #orgList .search-group .el-input__inner{
        height:38px;
    }
    #orgList .refresh{
        float:right;
        margin:10px 0;
    }
    #orgList .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #orgList .refresh .fa-refresh:hover{
        opacity:0.7;
    }
</style>


