<template>
    <main id="bind" class="container">
        <header class="head-title">
            <p>
                <span>{{title}}云比赛账号</span>
            </p>
        </header>
        <div></div>
        <el-form ref="info" :model="info" :rules="rules" label-position="left" label-width="108px">
            <el-form-item prop="loginName" label="已绑定用户名" v-show="bind">
                <el-input placeholder="请输入账号，如:123456@qq.com" :disabled="bind" v-model="loginName" :maxlength="50"></el-input>
            </el-form-item>
            <el-form-item prop="account" label="云比赛账号">
                <el-input placeholder="请输入账号，如:123456@qq.com" :disabled="bind" v-model="info.account"></el-input>
            </el-form-item>
            <el-form-item prop="pwd" v-if="!bind" label="云比赛密码">
                <el-input type="password" auto-complete="new-password" v-model="info.pwd" placeholder="请输入密码"></el-input>
            </el-form-item>
                <p class="bind-tip" v-if="!bind && $store.state.total.set.child.signUpYunbisai[1]">还没有云比赛机构账号？<span class="text-blue" @click="signUp('regInfo')">点此注册</span></p>
                <p class="tip" v-if="!bind">提示：绑定云比赛机构账号后才可以使用赛事功能。（请使用主账号绑定）</p>
            <el-form-item>
                <el-button v-if="!bind" type="primary" @click="bindAccount" :disabled="isAjax" :loading="isAjax">{{title}}</el-button>
            </el-form-item>
        </el-form>
        <el-dialog title="解绑机构账号" v-model="visible" class="text-center" size="tiny">
            <p class="dialog-title">是否确认解绑账号?</p>
            <p class="dialog-tip">解绑后将无法使用赛事管理功能。可再次绑定</p>
            <el-button size="small" @click="visible=false">取消</el-button>
            <el-button size="small" type="primary" @click="unbindAccount" :disabled="isAjax" :loading="isAjax">确认</el-button>
        </el-dialog>
        <el-dialog title=""
                   class="sign-container"
                   v-model="signVis">
            <div>
                <p class="reg-title">
                    <img style="width:40px" src="./../../assets/img/longshe_logo.png"
                         alt="logo">
                    <label>云比赛网 - 机构注册</label>
                    <img style="position:absolute;z-index:-1;top:0;left:0;width:100%;height:100%"
                         src="./../../assets/img/longshe_bg.png" alt="logo">
                </p>
                <el-form label-position="top"
                         :model="regInfo"
                         ref="regInfo"
                         class="reg-form"
                         :rules="regRules">
                    <el-form-item label=""
                                  prop="email">
                        <el-input class="form-input"
                                  v-model="regInfo.email"
                                  :maxlength="30"
                                  auto-complete="off"
                                  placeholder="请输入您的邮箱"></el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="name">
                        <el-input class="form-input"
                                  :maxlength="18"
                                  v-model="regInfo.name"
                                  auto-complete="off"
                                  placeholder="请输入您的机构名称"></el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="code">
                        <el-input v-model="regInfo.code" placeholder="请输入邮箱收到的验证码" :maxlength="6">
                            <el-button :style="{cursor:(codeBtn.isAjax || codeBtn.text.includes('重新') ? 'not-allowed' : 'pointer')}"
                                       slot="append"
                                       @click="getCode"
                                       :disabled="codeBtn.isAjax || codeBtn.text.includes('重新')"
                                       :loading="codeBtn.isAjax">{{codeBtn.text}}</el-button>
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="county">
                        <el-select v-model="regInfo.province"
                                   placeholder="请选择省份"
                                   disabled
                                   required>
                            <el-option v-for="item in proArr"
                                       :key="item[0]"
                                       :label="item[1]"
                                       :value="item[0]"></el-option>
                        </el-select>
                        <el-select v-model="regInfo.city"
                                   placeholder="请选择城市"
                                   @change="getArea('city')" required>
                            <el-option v-for="item in citArr"
                                       :key="item[0]"
                                       :label="item[1]"
                                       :value="item[0]"></el-option>
                        </el-select>
                        <el-select v-model="regInfo.county"
                                   placeholder="请选择县区"
                                   required>
                            <el-option v-for="item in couArr"
                                       :key="item[0]"
                                       :label="item[1]"
                                       :value="item[0]"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="address">
                        <el-input v-model="regInfo.address"
                                  :maxlength="100"
                                  placeholder="请输入具体街道门牌">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="password">
                        <el-input v-model="regInfo.password"
                                  :maxlength="18"
                                  type="password"
                                  auto-complete="new-password"
                                  placeholder="请输入密码">
                        </el-input>
                    </el-form-item>
                    <el-form-item label=""
                                  prop="repeatPassword">
                        <el-input v-model="regInfo.repeatPassword"
                                  :maxlength="18"
                                  type="password"
                                  auto-complete="new-password"
                                  placeholder="确认密码">
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary"
                                   @click="confirmSignUp('regInfo')">注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    export default{
        data(){
            const that = this;
            let $root = that.$parent;
            let _proArr = $root.cityQuery('','province','get');
            let _citArr = that.formatArea($root.cityQuery(that.$store.state.pro,'province','linkage'));
            let _couArr = that.formatArea($root.cityQuery(that.$store.state.pro+'01','city','linkage'));

            let checkEmail = (rule,value,callback) => {
                value = value.replace(/\s/g,'');
                if(value === ''){
                    callback(new Error('请输入邮箱'));
                }else if(!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/.test(value)){
                    callback(new Error('邮箱格式不正确'));
                }else{
                    callback();
                }
            };

            let checkPassword = (rule,value,callback) => {
                value = value.replace(/\s/g,'');
                if(value === ''){
                    callback(new Error('请再次输入密码'));
                }else if(value.length < 6 || value.length > 18){
                    callback(new Error('密码长度为6-18位'));
                }else if(value!==that.regInfo.password){
                    callback(new Error('两次密码不一致'));
                }else{
                    callback();
                }
            };
            return {
                proArr:_proArr,
                citArr:_citArr,
                couArr:_couArr,
                visible:false,
                title:'绑定',
                isAjax:false,
                bind:false,
                signVis:false,
                signTime:'',
                loginName:'',
                info:{
                    account:'',
                    pwd:''
                },
                rules:{
                    account:[
                        {required:true,message:'请输入账号',trigger:'blur'},
                        {min:3,max:50,message:'账号长度为3-50位',trigger:'blur'}
                    ],
                    pwd:[
                        {required:true,message:'请输入密码',trigger:'blur'},
                        {min:6,max:18,message:'密码长度为6-18位',trigger:'blur'}
                    ]
                },
                regInfo:{
                    email:'',
                    name:this.$store.state.orgName,
                    code:'',
                    province:'',
                    city:'',
                    county:'',
                    address:'',
                    password:'',
                    repeatPassword:''
                },
                codeBtn:{
                    isAjax:false,
                    text:'发送验证码'
                },
                regRules:{
                    email:[
                        {required:true,validator:checkEmail,trigger:'blur'}
                    ],
                    name:[
                        {required:true,message:'请输入机构名称',trigger:'blur'}
                    ],
                    code:[
                        {required:true,message:'请输入验证码',trigger:'blur'}
                    ],
                    province:[
                        {required:true,message:'请选择省份',trigger:'change'}
                    ],
                    city:[
                        {required:true,message:'请选择城市',trigger:'change'}
                    ],
                    county:[
                        {required:true,message:'请输入县区',trigger:'change'}
                    ],
                    address:[
                        {required:true,message:'请输入详细地址',trigger:'blur'}
                    ],
                    password:[
                        {required:true,message:'请输入密码',trigger:'blur'},
                        {min:6,max:18,message:'密码长度为6-18位',trigger:'blur'}
                    ],
                    repeatPassword:[
                        {required:true,validator:checkPassword,trigger:'blur'}
                    ]
                }
            }
        },
        methods:{
            formatArea(arr){
                arr.forEach((e,i) => {
                    if(e[0].includes(',')){
                        e[0] = e[0].substring(0,e[0].indexOf(','));
                    }
                });
                return arr;
            },
            bindAccount(){
                const that = this;
                const __url = that.$store.state.total.set.child.bind[1];
                if(__url==='') return false;
                if(!that.bind){
                    that.isAjax = true;
                    that.$ajax({
                        url:__url,
                        params:{
                            name:that.info.account,
                            pwd:that.info.pwd
                        }
                    }).then(res=>{
                        that.isAjax = false;
                        const _data = res.data;
                        if((_data.errcode>>0)===0){
                            that.$message.success(_data.msg);
                            that.bind = true;
                            that.text = '云比赛用户名';
                            that.$parent.refreshToken();
                        }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                            that.$parent.overTime();
                        }else{
                            that.$message.warning(_data.msg);
                        }
                    },res=>{
                        that.isAjax = false;
                        that.$message.warning('网络似乎出了点问题');
                    });
                }else{
                    that.visible = true;
                }
            },
            unbindAccount(){
                const that = this;
                const __url = that.$store.state.total.set.child.unbind[1];
                if(__url==='') return false;
                that.isAjax = true;
                if(that.bind){
                    that.$ajax({
                        url:__url,
                        params:{
                            pwd:that.info.pwd
                        }
                    }).then(res=>{
                        that.isAjax = false;
                        that.visible= false;
                        const _data = res.data;
                        if((_data.errcode>>0)===0){
                            that.$message.success(_data.msg);
                            that.bind = false;
                            that.title = '绑定';
                            that.info = {
                                account:'',
                                pwd:''
                            };
                            that.$parent.refreshToken();
                        }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                            that.$parent.overTime();
                        }else{
                            that.$message.warning(_data.msg);
                        }
                    },res=>{
                        that.isAjax = false;
                        that.visible= false;
                        that.$message.warning('网络似乎出了点问题');
                    });
                }
            },
            signUp(formName){
                const that = this;
                that.signVis = true;
                try{
                    this.$refs[formName].resetFields();
                }catch(error){

                }
            },
            getCode(){
                const that = this;
                const __url = that.$store.state.sendEmail;
                if(__url==='') return false;
                if(!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/.test(that.regInfo.email)){
                    that.$message.warning('请先输入邮箱');
                    return false;
                }
                that.codeBtn.isAjax = true;
                that.$ajax({
                    url:__url,
                    params:{
                        mail:that.regInfo.email
                    }
                }).then( res => {
                    const _data = res.data;
                    if( (_data.errcode>>0) === 0 ){
                        that.$message.success(_data.msg);
                        that.codeBtn.isAjax = false;
                        let t = 60;
                        const timer = setInterval( () => {
                            that.codeBtn.text = '重新发送 ' + t-- + 's';
                            if(t<=0){
                                that.codeBtn.text = '发送验证码';
                                clearInterval(timer);
                            }
                        },1000);
                    }else if( (_data.errcode>>0) === 1000 || (_data.errcode>>0) === 1001 ){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                        that.codeBtn.isAjax = false;
                    }
                },res => {

                });
            },
            confirmSignUp(formName){
                const that = this;
                const __url = that.$store.state.regOrg;
                if(__url==='') return false;
                that.$refs[formName].validate((valid) => {
                    if(valid){
                        that.$ajax({
                            url:__url,
                            params:{
                                email:that.regInfo.email,
                                cname:that.regInfo.name,
                                password:that.regInfo.password,
                                repassword:that.regInfo.repeatPassword,
                                vercode:that.regInfo.code,
                                county_id:that.regInfo.county,
                                address:that.regInfo.address,
                            }
                        }).then(res=>{
                            const _data = res.data;
                            if((_data.errcode>>0)===0){
                                // that.$parent.openNewTag();
                                that.signVis = false;
                                that.signTime = _data.msg;
                                that.$notify.success({
                                    title:'注册成功',
                                });
                                that.info.account = that.regInfo.email;
                                // window.open('http://www.yunbisai.com/orgRegister?t=' + _data.msg);
                                // 注入机构名称 和 地址
                                //window.frames["signUp"].document.getElementById("orgName").value = 123;
                            }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001){
                                that.$parent.overTime();
                            }else{
                                that.$message.warning(_data.msg);
                            }
                        },res=>{
                            that.$message.warning('网络似乎出了点问题');
                        });
                    }
                });
            },
            getArea(type){
                const $root = this.$parent;
                const that = this;
                if(type === 'province'){
                    this.citArr = that.formatArea($root.cityQuery(this.regInfo.province,'province','linkage'));
                    this.regInfo.city = that.citArr[0][0];
                    //this.regInfo.county = that.couArr[0][0];
                    //  this.couArr = $root.cityQuery(this.citArr[0][0],'city','linkage');
                }else if(type === 'city'){
                    that.couArr = that.formatArea($root.cityQuery(that.regInfo.city,'city','linkage'));
                    that.regInfo.county = that.couArr[0][0];
                }
                if(that.regInfo.city.includes(',')){
                    that.regInfo.city = that.regInfo.city.substring(0,that.regInfo.city.indexOf(','));
                }
                if(that.regInfo.county.includes(',')){
                    that.regInfo.county = that.regInfo.county.substring(0,that.regInfo.county.indexOf(','));
                }
            },
            getInfo(){
                const that = this;
                const __url = that.$store.state.total.org.child.orgDetail[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url
                }).then( res => {
                    const _data = res.data;
                    if( (_data.errcode>>0) === 0){
                        const dat = _data.data;
                        if( dat.county.length === 6 ) {
                            that.regInfo.province = dat.province+'';
                            that.getArea('province');
                            that.regInfo.city = dat.city+'';
                            that.getArea('city');
                            that.regInfo.county = dat.county+'';
                        }
                        that.regInfo.name = dat.org_name;
                        that.regInfo.address = dat.address;
                    }
                }, res => {

                });
            }
        },
        mounted(){
            const that = this;
            const __url = that.$store.state.total.set.child.bindInfo[1];
            if(__url==='') return false;
            that.$ajax({
                url:__url,
            }).then(res=>{
                const _data = res.data;
                if((_data.errcode>>0)===0){
                    const dat = _data.data;
                    if(dat!==null){
                        that.info.account = dat.loginEmail;
                        that.loginName = dat.loginName;
                        that.bind = true;
                        //that.title = '解绑';
                    }
                }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                    that.$parent.overTime();
                }else{
                    that.$message.warning(_data.msg);
                }
                },res=>{
               that.$message.warning('网络似乎出了点问题');
            });

            that.getInfo();
        }
    }
</script>
<style>
    #bind .text-blue{
        color:#20A0FF;
        cursor:pointer;
    }
    #bind .text-blue:hover{
        opacity:0.8;
    }
    #bind .bind-tip{
        font-size:16px;
        text-align:left;
    }
    #bind{
        background:#fff;
        padding:10px 20px;
    }
    #bind .el-form{
        width:50%;
        max-width:400px;
        text-align:center;
    }
    #bind .reg-form{
        width:90%;
        max-width:90%;
        padding:0 5%;
    }
    #bind .reg-form .el-select{
        width:31%;
    }
    #bind .reg-title{
        text-align:center;
        font-size:16px;
        margin-bottom:10px;
    }
    #bind .tip{
        font-size:12px;
        margin-bottom:6px;
        text-align:left;
    }
    #bind .dialog-title{
        font-size:14px;
    }
    #bind .dialog-tip{
        font-size:12px;
        color:#999;
        margin:10px;
    }
    #bind .sign-container .el-dialog .el-input{
        width:100%;
    }
    #bind .sign-container .el-dialog__body{
        padding:0 0 20px 0;
    }
</style>


