<!-- 待确认名单 -->
<template>
    <main id="c_wait" class="container">
        <section class="search-area"
                 @keyup.enter="search">
            <el-input class="search-group"
                      v-model="key.keyword"
                      placeholder="请输入姓名或证书编号或证件号或隶属机构">
                <el-select style="width:120px;"
                           slot="prepend"
                           filterable
                           v-model="key.rank"
                           placeholder="棋手级别"
                           @change="search">
                    <el-option-group v-for="group in optionGroups"
                                     :key="group.label"
                                     :label="group.label">
                        <el-option v-for="item in group.options"
                                   :label="item.label"
                                   :value="item.value"
                                   :key="item.value"></el-option>
                    </el-option-group>
                </el-select>
                <el-button slot="append"
                           icon="search"
                           @click="search"
                           :disabled="isSearch"
                           :loading="isSearch">查询</el-button>
            </el-input>
        </section>
        <div class="wait">
            <div style="margin-right:20px;text-align:right">
                <span class="tip"
                      v-if="!$store.state.total.cre.child.confirmInfo[1]">
                        提示：确认信息后可前往 <router-link to="/Main/rankingList">段级位名单</router-link> 页缴费申请证书。
                    </span>
                <div class="refresh">
                    <i class="fa fa-refresh"
                       @click="getData"></i>
                </div>
                <el-button class="mem-submit"
                           type="success"
                           v-if="$store.state.total.cre.child.confirmInfo[1]"
                           :disabled="!allMemberInfo.length||!multipleSelection.length"
                           @click="confirmInfo">确认信息</el-button>
            </div>
            <!--<p class="money">-->
                <!--总计：-->
                <!--<span class="text-red">-->
                    <!--{{(money-0).toFixed(2)}}-->
                <!--</span>元-->
            <!--</p>-->
            <p class="tip"
               v-if="$store.state.total.cre.child.confirmInfo[1]">
                提示：确认信息后可前往 <router-link to="/Main/rankingList">段级位名单</router-link> 页缴费申请证书。
            </p>
            <section class="table-area">
                <el-table ref="multipleTable"
                          :data="allMemberInfo"
                          stripe
                          :loading="isAjax"
                          @selection-change="handleSelectionChange">
                    <el-table-column type="selection"
                                     min-width="55"></el-table-column>
                    <el-table-column label="会员信息"
                                     min-width="240">
                        <template scope="scope">
                            <p>姓名：{{scope.row.name}}</p>
                            <p>证件类型：{{scope.row.cardType}}</p>
                            <p>证件号：{{scope.row.ID}}</p>
                            <p>段级位：{{scope.row.rank}}</p>
                            <p>隶属机构：{{scope.row.org}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column label="赛事信息"
                                     min-width="300">
                        <template scope="scope">
                            <p>名次：{{scope.row.ranking}}</p>
                            <p>赛事名称：{{scope.row.title}}</p>
                            <p>赛事组别：{{scope.row.group}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="fee"
                                     label="证书费（元）"
                                     min-width="120">
                        <template scope="scope">
                            <span class="text-red">{{scope.row.fee}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作"
                                     min-width="80">
                        <template scope="scope">
                            <el-button type="text"
                                       v-if="$store.state.total.cre.child.waitEdit[1]"
                                       @click="watch(scope.row.id,scope.row.name,scope.row.rank,scope.row.ranking)">编辑</el-button>
                            <br>
                            <el-button type="text"
                                       class="text-red"
                                       v-if="$store.state.total.cre.child.waitDel[1]"
                                       slot="reference"
                                       @click="del(scope.row.id,scope.row.name)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination class="text-center"
                               @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[20, 50, 100]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </section>
        </div>
        <el-dialog size="tiny"
                   title="选择支付方式"
                   v-model="payVis">
            <div class="type-area">
                <div class="ali"
                     @click="$parent.Pay('ali',orderID,'/Main/apply?pay')">
                    <img src="./../../assets/img/alipay.png" alt="ali">
                    <p>支付宝</p>
                </div>
                <div class="wechat"
                     @click="$parent.Pay('wx',orderID,'/Main/apply?pay')">
                    <img src="./../../assets/img/wechat.png" alt="wechat">
                    <p>微信</p>
                </div>
                <div class="wallet"
                     @click="" v-if="false">
                    <img src="./../../assets/img/walletpay.png"
                         alt="wallet">
                    <p>钱包余额</p>
                </div>
            </div>
            <p class="tip">提示：支付证书费用后，省级机构将收到申请并下发证书。</p>
        </el-dialog>
        <el-dialog size="tiny"
                   title="删除人员"
                   v-model="delVis">
            <p>
                是否确认删除
                <span class="delName">{{delName}}</span>
                ？
            </p>
            <p class="tip"
               style="text-align:left">提示：删除后无法恢复！</p>
            <div class="text-center">
                <el-button size="small" @click="delVis = false">取消</el-button>
                <el-button size="small" type="primary" @click="confirmDel">确认</el-button>
            </div>
        </el-dialog>
        <el-dialog size="tiny"
                   title="修改人员"
                   v-model="editVis">
            <el-form label-position="left"
                     label-width="60px">
                <el-form-item label="姓名">
                    <el-input v-model="editInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="级别"
                              position="left">
                    <el-select class="form-input"
                               filterable
                               v-model="editInfo.rank"
                               placeholder="请选择棋手级别">
                        <el-option-group v-for="group in optionGroup"
                                         :key="group.label"
                                         :label="group.label">
                            <el-option v-for="item in group.options"
                                       :label="item.label"
                                       :value="item.value"
                                       :key="item.value"></el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
                <el-form-item label="名次"
                              position="left">
                    <el-input v-model="editInfo.no"></el-input>
                </el-form-item>
            </el-form>
            <div class="text-center">
                <el-button size="small"
                           @click="editVis = false">取消</el-button>
                <el-button size="small"
                           type="primary"
                           @click="confirmEdit">确认</el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    export default{
        data(){
            return {
                editId:'',
                editInfo:{
                    name:'',
                    rank:'',
                    no:''
                },
                key:{
                    keyword:'',
                    rank:''
                },
                editVis:false,
                delName:'',
                delVis:false,
                payVis:false,
                isAjax:false,
                isSearch:false,
                isEdit:false,
                tinyAjax:false,
                dialogFormVisible:false,
                picURL:'',
                sameID:false,
                multipleSelection:[],
                tableHei:(window.outerHeight-420)<430?430:(window.outerHeight-420),
                currentPage:1,
                pageSize:20,
                totalNum:0,
                money:0,
                pickerOptions:{
                    disabledDate(time){
                        return time.getTime() >= this.$store.state.sysTime - 8.64e7;
                    }
                },
                gotAge:'',
                gotGender:1,
                allMemberInfo:[],
                optionGroups:[
                    {
                        label:'全部',
                        options:[{value:'',label:'全部'}]
                    },
                    {
                        label:'级别',
                        options:[
                            {value:99,label:'25级'},
                            {value:103,label:'24级'},
                            {value:104,label:'23级'},
                            {value:106,label:'22级'},
                            {value:107,label:'21级'},
                            {value:108,label:'20级'},
                            {value:109,label:'19级'},
                            {value:110,label:'18级'},
                            {value:111,label:'17级'},
                            {value:112,label:'16级'},
                            {value:113,label:'15级'},
                            {value:114,label:'14级'},
                            {value:115,label:'13级'},
                            {value:116,label:'12级'},
                            {value:117,label:'11级'},
                            {value:118,label:'10级'},
                            {value:119,label:'9级'},
                            {value:120,label:'8级'},
                            {value:121,label:'7级'},
                            {value:122,label:'6级'},
                            {value:123,label:'5级'},
                            {value:124,label:'4级'},
                            {value:125,label:'3级'},
                            {value:126,label:'2级'},
                            {value:127,label:'1级'},
                        ]
                    },
                    {
                        label:'业余段位',
                        options:[
                            {value:128,label:'1段'},
                            {value:129,label:'2段'},
                            {value:130,label:'3段'},
                            {value:131,label:'4段'},
                            {value:132,label:'5段'},
                            {value:133,label:'6段'},
                        ]
                    }
                ],
                optionGroup:[
                    {
                        label:'级别',
                        options:[
                            {value:99,label:'25级'},
                            {value:103,label:'24级'},
                            {value:104,label:'23级'},
                            {value:106,label:'22级'},
                            {value:107,label:'21级'},
                            {value:108,label:'20级'},
                            {value:109,label:'19级'},
                            {value:110,label:'18级'},
                            {value:111,label:'17级'},
                            {value:112,label:'16级'},
                            {value:113,label:'15级'},
                            {value:114,label:'14级'},
                            {value:115,label:'13级'},
                            {value:116,label:'12级'},
                            {value:117,label:'11级'},
                            {value:118,label:'10级'},
                            {value:119,label:'9级'},
                            {value:120,label:'8级'},
                            {value:121,label:'7级'},
                            {value:122,label:'6级'},
                            {value:123,label:'5级'},
                            {value:124,label:'4级'},
                            {value:125,label:'3级'},
                            {value:126,label:'2级'},
                            {value:127,label:'1级'},
                        ]
                    },
                    {
                        label:'业余段位',
                        options:[
                            {value:128,label:'1段'},
                            {value:129,label:'2段'},
                            {value:130,label:'3段'},
                            {value:131,label:'4段'},
                            {value:132,label:'5段'},
                            {value:133,label:'6段'},
                        ]
                    }
                ],
            }
        },
        methods:{
            getCardTypeName (num){
                switch (num>>0){
                    case 1:
                        return '身份证';
                    case 2:
                        return '护照';
                    case 4:
                        return '台胞证';
                    case 8:
                        return '港澳通行证';
                }
            },
            getRankVal(val){
                const that = this;
                let res = '';
                that.optionGroup.forEach((e,i)=>{
                    e.options.forEach((element,index)=>{
                        if(val===element.label){
                            res = element.value;
                        }
                    });
                });
                return res;
            },
            watch(id,name,rank,no){
                const that = this;
                that.editId = id;
                that.editVis = true;
                that.editInfo = {
                    name:name,
                    rank:that.getRankVal(rank),
                    no:no
                }
            },
            del(id,name){
                const that = this;
                that.editId = id;
                that.delName = name;
                that.delVis = true;
            },
            confirmEdit(){
                const that = this;
                const __url = that.$store.state.total.cre.child.waitEdit[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        logs_id:that.editId,
                        levelID:that.editInfo.rank,
                        ranking:that.editInfo.no,
                        idCardName:that.editInfo.name
                    }
                }).then(res=>{
                    that.delVis = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('修改成功');
                        that.editVis = false;
                        that.getData();
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.delVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            confirmDel(){
                const that = this;
                const __url = that.$store.state.total.cre.child.waitDel[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        logs_id:that.editId
                    }
                }).then(res=>{
                    that.delVis = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('删除成功');
                        that.getData();
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.delVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            handleSizeChange(pageSize){
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            search(){
                this.getData();
            },
            getAge(idCard){
                if(idCard.length===15){
                    this.gotGender = idCard[14]%2===0?'2':'1';
                    let _year = '19'+idCard.substring(6,8);
                    this.gotAge = ((new Date() - new Date(_year.substring(0,4),_year.substring(4,6)-1,_year.substring(6,8)))/1000/60/60/24/365)>>0;
                }else if(idCard.length===18){
                    this.gotGender = idCard[16]%2===0?'2':'1';
                    let _year = idCard.substring(6,14);
                    this.gotAge = ((new Date() - new Date(_year.substring(0,4),_year.substring(4,6)-1,_year.substring(6,8)))/1000/60/60/24/365)>>0;
                }
            },
            handleSelectionChange(val){
                const that = this;
                let idArr = [],
                    feeArr = [],
                    number = 0;
                that.multipleSelection = val;
                that.money = 0;
                that.multipleSelection.forEach((e,i)=>{
                    that.money += (e.price - 0);
                });
            },
            getData(){
                const that = this;
                const __url = that.$store.state.total.cre.child.waitList[1];
                if(__url==='') return false;
                that.isAjax = true;
                that.$ajax({
                    url:__url,
                    params:{
                        page:that.currentPage,
                        pageSize:that.pageSize,
                        level:that.key.rank,
                        keywords:that.key.keyword,
                        check_state:0
                    }
                }).then(res=>{
                    const _data = res.data;
                    that.isAjax = false;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.allMemberInfo = [];
                        dat.forEach((e,i)=>{
                            that.allMemberInfo.push({
                                index    : e.ROW_NUMBER,
                                ID       : e.idCard,
                                cardType : that.getCardTypeName(e.cardType>>0),
                                id       : e.logs_id,
                                name     : e.idCardName,
                                rank     : e.type_name,
                                fee      : (e.price-0).toFixed(2),
                                ranking  : e.ranking===null?'暂无名次':e.ranking,
                                price    : e.price,
                                title    : e.title,
                                org      : e.org_name,
                                group    : e.groupName,
                                del      : false,
                            });
                        });
                       /* /////////////////////////////////////
                        that.allMemberInfo.push({
                            index    : 1,
                            ID       : 11111,
                            cardType : that.getCardTypeName(1>>0),
                            id       : 1,
                            name     : 1111,
                            rank     : 101,
                            fee      : 1,
                            ranking  : '第二名',
                            price    : 10,
                            title    : '比赛',
                            org      : '浙江省围棋协会',
                            group    : '青年组',
                            del      : false,
                        });
                        ////////////////////////////////////*/
                        that.totalNum = (_data.total>>0);
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        this.$message.warning(_data.msg);
                    }
                },res=>{
                    that.isAjax = false;
                    this.$message.warning('网络似乎出了点问题');
                });
            },
            findIndex(id){
                let a='';
                this.allMemberInfo.forEach((e,i)=>{
                    if(e.id===id){
                        a = i;
                        return false;
                    }
                });
                return a;
            },
            formatDate(t){
                const _t = new Date(t);
                const f=(a)=>{
                    return a<10?'0'+a:a;
                };
                return _t.getFullYear()+'-'+f(_t.getMonth()+1)+'-'+f(_t.getDate())+' '+f(_t.getHours())+':'+f(_t.getMinutes());
            },
            confirmAdd(formName){
                const that = this;
                that.$refs[formName].validate((valid) => {
                    if(valid){
                        if(that.isEdit){
                            that.edit();
                        }else{
                            that.add();
                        }
                    }
                });
            },
            confirmInfo(){
                const that = this;
                const __url = that.$store.state.total.cre.child.confirmInfo[1];
                if(__url==='') return false;
                let idArr=[];
                that.multipleSelection.forEach((e,i)=>{
                    idArr.push(e.id);
                });
                that.$ajax({
                    url:__url,
                    params:{
                        logs_id:idArr.join(','),
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$message.success(_data.msg);
                        that.getData();
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            }
        },
        mounted(){
            this.getData();
        }
    }
</script>
<style>
    #c_wait .tip a{
        color:#20a0ff;
    }
    #c_wait .text-red{
        color:#ff4949;
    }
    #c_wait .text-red:hover{
        opacity:0.7;
    }
    #c_wait .delName{
        color:#20a0ff;
        font-size:18px;
    }
    #c_wait .tip{
        color:#8492A6;
        font-size:12px;
        margin:4px 20px;
        text-align:right;
    }
    #c_wait .upload-img{
        width:100%;
        height:100%;
    }
    #c_wait .wait{
        overflow: hidden;
        position: relative;
        padding:20px;
    }
    #c_wait .wait .head-title p{
        margin:10px 0;
    }
    #c_wait .table-area{
        margin-bottom:20px;
    }
    #c_wait .add-panel{
        padding:15px;
        cursor:pointer;
        border-bottom:1px solid #d3dce6;
    }
    #c_wait .add-panel:last-of-type{
        border-bottom:0;
    }
    #c_wait .add-panel .plus{
        display: inline-block;
        width:48px;
        height:48px;
        font-size:68px;
        line-height:38px;
        color:#fff;
        background: #999;
        margin-right:18px;
    }
    #c_wait .add-panel .mem-info{
        position: relative;
        top:-16px;
        color:#999;
    }
    #c_wait .mem-submit{

    }
    #c_wait .money{
        position: absolute;
        font-size:18px;
    }
    #c_wait .mem-dialog{
        min-width:500px;
    }
    #c_wait .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #c_wait .mem-dialog .mem-form .form-input.age{
        width:100px;
    }
    #c_wait .refresh{
        display:inline-block;
        margin:10px 0;
    }
    #c_wait .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #c_wait .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #c_wait .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #c_wait .el-icon-more {
        margin-left:14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius:0;
    }*/
    #c_wait .search-area .add-new{
        margin-left:20px;
    }
    #c_wait .search-area .search-group{
        width:80%;
    }
    #c_wait .type-area{
        text-align:center;
    }
    #c_wait .type-area .wechat,#c_wait .type-area .ali,#c_wait .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
</style>


