<template>
    <main id="fee" class="container">
       <p class="title">费用设置</p>
        <el-collapse v-model="activeNames">
            <el-collapse-item title="注册费" name="1">
                <div class="fee-item" v-for="item in sign">
                    <span class="mark">{{item.mark}}：</span>
                    <el-input v-model="item.price" @input="edit(item.type,item.id)" :maxlength="8"></el-input>
                    <span>{{tip}}</span>
                    <el-button  class="fee-submit" type="primary" size="small" :loading="isAjax" v-show="item.edit" @click="submit(item.price,item.id,item)">提交</el-button>
                </div>
            </el-collapse-item>
            <el-collapse-item title="年检费" name="2">
                <div class="fee-item" v-for="item in year">
                    <span class="mark">{{item.mark}}：</span>
                    <el-input v-model="item.price" @input="edit(item.type,item.id)" :maxlength="8"></el-input>
                    <span>{{tip}}</span>
                    <el-button  class="fee-submit" type="primary" size="small" :loading="isAjax" v-show="item.edit" @click="submit(item.price,item.id,item)">提交</el-button>
                </div>
            </el-collapse-item>
            <el-collapse-item title="证书费用" name="3">
                <div class="fee-item" v-for="item in cer">
                    <span class="mark">{{item.mark}}：</span>
                    <el-input v-model="item.price" @input="edit(item.type,item.id)" :maxlength="8"></el-input>
                    <span>{{tip}}</span>
                    <el-button class="fee-submit" type="primary" size="small" :loading="isAjax" v-show="item.edit" @click="submit(item.price,item.id,item)">提交</el-button>
                </div>
            </el-collapse-item>
        </el-collapse>
    </main>
</template>
<script>
    export default{
        data(){
            return {
                isAjax:false,
                activeNames:['1','2','3'],
                sign:[],
                year:[],
                cer:[],
                isEdit:{},
                tip:' 元 如有需要请修改，费用以当前显示的数值为准'
            }
        },
        methods:{
            submit(price,id,item){
                const that = this;
                const __url = that.$store.state.total.set.child.feeEdit[1];
                if(__url==='') return false;
                that.isAjax = true;
                that.$ajax({
                    url:__url,
                    method:'post',
                    params:{
                        id:id,
                        price:price
                    }
                }).then(res=>{
                    that.isAjax = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('修改成功');
                        item.edit = false;
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message(_data.msg);
                        that.$router.push('/login');
                    }
                },res=>{
                    that.isAjax = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            edit(type,id){
                const that = this;
                const find = (arr,id)=>{
                    arr.forEach((e,i)=>{
                        if(e.id===id){
                            e.edit = 1;
                            return false;
                        }
                    });
                };
                switch(type){
                    case '1':find(that.sign,id);break;
                    case '2':find(that.year,id);break;
                    case '3':find(that.cer,id);break;
                }
            },
            addItem(obj,item){
                obj.push({
                    id:item.id,
                    type:item.type,
                    price:parseFloat(item.price),
                    mark:item.mark,
                    edit:0
                });

            }
        },
        mounted(){
            const that = this;
            const __url = that.$store.state.total.set.child.feeList[1];
            if(__url==='') return false;
            that.$ajax({
                url:__url
            }).then(res=>{
                const _data = res.data,
                      dat = _data.data;
                if((_data.errcode>>0)===0){
                    dat.forEach((e,i)=>{
                        switch(e.type){
                            case '1':that.addItem(that.sign,e);break;
                            case '2':that.addItem(that.year,e);break;
                            case '3':that.addItem(that.cer,e);break;
                        }
                    });
                }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                    that.$parent.overTime();
                }else{
                    that.$message.warning(_data.msg);
                }
            },res=>{
                that.$message.warning('网络开小差了');
            });
        }
    }
</script>
<style>
    #fee{
        background:#fff;
    }
    #fee .title{
        font-size:20px;
        text-align:center;
        margin:10px 0;
    }
    #fee .el-input{
        width:80px;
    }
    #fee .fee-item{
        margin:10px 0;
        color:#999;
    }
    #fee .mark{
        width:66px;
        display:inline-block;
    }
    #fee .fee-submit{
        margin-left:40px;
    }
</style>


