<!-- 证书列表 -->
<template>
    <main id="c_apply" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入姓名或注册证号或证件号或隶属机构">
                <el-select style="width:120px;" slot="prepend" filterable v-model="key.rank" placeholder="棋手级别" @change="search">
                    <el-option-group v-for="group in optionGroups" :key="group.label" :label="group.label">
                        <el-option v-for="item in group.options" :label="item.label" :value="item.value" :key="item.value"></el-option>
                    </el-option-group>
                </el-select>
                <el-button slot="append" icon="search" @click="search" :disabled="isSearch" :loading="isSearch">查询</el-button>
            </el-input>
        </section>
        <div class="addMem">
            <section class="add-area">
                <el-tabs v-model="select" type="card" @tab-click="switchTab">
                    <el-tab-pane class="plan" label="待打印" name="first">

                    </el-tab-pane>
                    <el-tab-pane class="plan" label="已打印" name="second">

                    </el-tab-pane>
                </el-tabs>
                <div style="clear:both"></div>
                <div style="margin:48px 20px 0 0;text-align:right;min-height:10px">
                    <!--<span class="tip">提示：打印证书名单后请点击“回填证书”按钮，填写相应的注册证号。</span>-->
                    <!--<div class="refresh">-->
                        <!--<i class="fa fa-refresh" @click="getData"></i>-->
                    <!--</div>-->
                    <el-button class="mem-submit" type="primary"
                               :disabled="!multipleSelection.length"
                               v-if="0"
                               @click="print">打印证书</el-button>
                </div>
                <el-table ref="multipleTable"
                          :data="allMemberInfo"
                          stripe
                          :loading="isAjax"
                          @cell-click="showOrgDetail"
                          @selection-change="handleSelectionChange">
                    <el-table-column v-if="0" type="selection" min-width="55"></el-table-column>
                    <el-table-column label="会员信息" min-width="240">
                        <template scope="scope">
                            <p>姓名：{{scope.row.name}}</p>
                            <p>证件类型：{{scope.row.cardType}}</p>
                            <p>证件号：{{scope.row.ID}}</p>
                            <p>注册证号：{{scope.row.certID}}</p>
                            <p>段级位：{{scope.row.rank}}</p>
                            <p>隶属机构：{{scope.row.org}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column label="赛事信息" min-width="300">
                        <template scope="scope">
                            <p>名次：{{scope.row.ranking}}</p>
                            <p>申请时间：{{scope.row.time}}</p>
                            <p>赛事名称：{{scope.row.title}}</p>
                            <p>赛事组别：{{scope.row.group}}</p>
                            <p>承办方：{{scope.row.applyOrgName}}</p>
                            <p>订单号：{{scope.row.orderID}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column v-if="0" label="操作" min-width="160">
                        <template scope="scope">
                            <el-button v-if="$store.state.total.cre.child.dumpCret[1]" type="text" @click="watch(scope.row.name,scope.row.ID,scope.row.id)">确认打印</el-button>
                            <br>
                            <el-button v-if="$store.state.total.cre.child.refuse[1]" class="text-red" type="text" @click="refuse(scope.row.name,scope.row.id)">不同意</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination class="text-center"
                               @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[20, 50, 100]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </section>
            <el-dialog size="tiny" title="不同意发证" v-model="editVis">
                <p>是否确认不同意给 <span class="text-blue"> {{editName}}</span> 发证？</p>
                <div style="height:10px"></div>
                <p class="tip" style="margin:0;text-align:left;">提示：一旦确认无法恢复！</p>
                <p class="tip" style="margin:0;text-align:left;">不同意发证后，证书费用将原路退回申请机构。</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="editVis = false">取 消</el-button>
                    <el-button type="primary" size="small" :loading="editAjax" @click="confirmRefuse">确 认</el-button>
                </div>
            </el-dialog>
            <el-dialog size="tiny" title="确认打印" v-model="dumpVis">
                <p>确认<span class="text-blue"> {{dumpInfo[0]}}</span><span class="tip" style="margin:0">（证件号：{{dumpInfo[1]}}）</span> 的证书已打印？</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="dumpVis = false">取 消</el-button>
                    <el-button type="primary" size="small" :loading="dumpAjax" @click="confirmDump">确 认</el-button>
                </div>
            </el-dialog>
            <el-dialog class="orgDetail" size="tiny" title="机构信息" v-model="orgDetailVis">
                <p>机构名称：{{orgDetail.orgName}}</p>
                <p>联 系 人&nbsp;：{{orgDetail.linkMan}}</p>
                <p>联系电话：{{orgDetail.telNum}}</p>
                <p>机构地址：{{orgDetail.address}}</p>

            </el-dialog>
            <!--<el-dialog size="tiny" title="确认打印" v-model="dumpVis">-->
                <!--<p>正在填写<span class="text-blue"> {{dumpInfo[0]}}</span><span class="tip" style="margin:0">（证件号：{{dumpInfo[1]}}）</span> 的注册证号：</p>-->
                <!--<div style="height:10px"></div>-->
                <!--<el-input size="small" v-model="creID" :maxlength="50"></el-input>-->
                <!--<p class="tip" style="margin:0;text-align:left;">提示：回填注册证号后，证书将记入库中。</p>-->
                <!--<p class="tip" style="margin:0;text-align:left;">同时，下级机构将能看到已发证书。</p>-->
                <!--<div style="height:10px"></div>-->
                <!--<div class="text-center">-->
                    <!--<el-button size="small" @click="dumpVis = false">取 消</el-button>-->
                    <!--<el-button type="primary" size="small" :loading="dumpAjax" @click="confirmDump">确 认</el-button>-->
                <!--</div>-->
            <!--</el-dialog>-->
        </div>
    </main>
</template>
<script>
    export default{
        data(){
            let hei = 0,win = window.outerWidth;
            if(win>1366){
                hei=620;
            }else if(win<=1366&&win>=1024){
                hei=430;
            }else{
                hei=300;
            }
            return {
                key:{
                    keyword:'',
                    rank:''
                },
                editId:'',
                editName:'',
                dumpVis:false,
                dumpAjax:false,
                dumpInfo:['','',''],
                creID:'',
                editVis:false,
                editAjax:false,
                isAjax:false,
                isSearch:false,
                tinyAjax:false,
                dialogFormVisible:false,
                picURL:'',
                sameID:false,
                multipleSelection:[],
                tableHei:hei,
                currentPage:1,
                pageSize:20,
                printState:0,
                totalNum:0,
                pickerOptions:{
                    disabledDate(time){
                        return time.getTime() >= this.$store.state.sysTime - 8.64e7;
                    }
                },
                gotAge:'',
                gotGender:1,
                allMemberInfo:[],
                optionGroups:[
                    {
                        label:'全部',
                        options:[{value:'',label:'全部'}]
                    },
                    {
                        label:'级别',
                        options:[
                            {value:143,label:'28级'},
                            {value:142,label:'27级'},
                            {value:141,label:'26级'},
                            {value:99,label:'25级'},
                            {value:103,label:'24级'},
                            {value:104,label:'23级'},
                            {value:106,label:'22级'},
                            {value:107,label:'21级'},
                            {value:108,label:'20级'},
                            {value:109,label:'19级'},
                            {value:110,label:'18级'},
                            {value:111,label:'17级'},
                            {value:112,label:'16级'},
                            {value:113,label:'15级'},
                            {value:114,label:'14级'},
                            {value:115,label:'13级'},
                            {value:116,label:'12级'},
                            {value:117,label:'11级'},
                            {value:118,label:'10级'},
                            {value:119,label:'9级'},
                            {value:120,label:'8级'},
                            {value:121,label:'7级'},
                            {value:122,label:'6级'},
                            {value:123,label:'5级'},
                            {value:124,label:'4级'},
                            {value:125,label:'3级'},
                            {value:126,label:'2级'},
                            {value:127,label:'1级'}
                        ]
                    },
                    {
                        label:'业余段位',
                        options:[
                            {value:128,label:'1段'},
                            {value:129,label:'2段'},
                            {value:130,label:'3段'},
                            {value:131,label:'4段'},
                            {value:132,label:'5段'},
                            {value:133,label:'6段'}
                        ]
                    }
                ],
                orgDetailVis:false,
                orgDetail:{
                    orgName:'',
                    telNum:'',
                    linkMan:'',
                    address:'',
                },
                select:'first',
            }
        },
        methods:{
            switchTab(e){
                const that =this;
                that.printState = e.index;
                that.getData();
            },
            getCardTypeName (num){
                switch (num>>0){
                    case 1:
                        return '身份证';
                    case 2:
                        return '护照';
                    case 4:
                        return '台胞证';
                    case 8:
                        return '港澳通行证';
                }
            },
            showOrgDetail(row,column,cell){
                const that = this;
                const _index = that.$store.state.orgLevel === 1 ? 8 : 7;
                if( cell.cellIndex === _index ){
                    that.orgDetailVis = true;
                    that.orgDetail = {
                        orgName : row.org,
                        telNum  : row.telNum,
                        linkMan : row.linkMan,
                        address : row.address,
                    }
                }
            },
            watch(name,ID,id){
                const that = this;
                that.dumpVis = true;
                that.dumpInfo = [name,ID,id];
            },
            confirmDump(){
                const that = this;
                const _id = that.dumpInfo[2];
                const __url = that.$store.state.total.cre.child.dumpCret[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    method:'post',
                    params:{
                        id:_id,
                        book_no:that.creID
                    }
                }).then(res=>{
                    that.dumpVis = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$message.success(_data.msg);
                        that.getData();
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            refuse(name,id){
                const that = this;
                that.editId = id;
                that.editName = name;
                that.editVis = true;
            },
            confirmRefuse(){
                const that = this;
                const __url = that.$store.state.total.cre.child.refuse[1];
                if(__url==='') return false;
                that.editAjax = true;
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.editId
                    }
                }).then(res=>{
                    that.editAjax = false;
                    that.editVis = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('操作成功');
                        that.getData();
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001) {
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                    },res=>{
                    that.editAjax = false;
                    that.editVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            handleSizeChange(pageSize){
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            search(){
                this.getData();
            },
            handleSelectionChange(val){
                const that = this;
                that.multipleSelection = val;
            },
            getRank(rank){
                rank = rank >>0;
                this.optionGroups.forEach((e,i)=>{
                    e.options.forEach((e,i)=>{
                        if(e.value===rank){
                            rank = e.label;
                            return false;
                        }
                    });
                });
                return rank;
            },
            print(){
                this.$notify.info('线上打印功能开发中，敬请期待！');
                //const that = this;
                //window.open('http://'+window.location.host+'/#/print');
            },
            getData(){
                const that = this;
                that.isAjax = true;
                that.$ajax({
                    url:that.$store.state.total.cre.child.creList[1],
                    params:{
                        page:that.currentPage,
                        pageSize:that.pageSize,
                        level:that.key.rank,
                        keywords:that.key.keyword,
                        print_state:that.printState,
                    }
                }).then(res=>{
                    const _data = res.data;
                    that.isAjax = false;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.allMemberInfo = [];
                        dat.forEach((e,i)=>{
                            // get county name
                            const _county =  that.$parent.cityQuery(e.county,'county','get');
                            // set all data
                            that.allMemberInfo.push({
                                index        : e.ROW_NUMBER,
                                ID           : e.idCard,
                                cardType     : that.getCardTypeName(e.cardType>>0),
                                id           : e.id,
                                certID       : e.book_no,
                                name         : e.idCardName,
                                rank         : that.getRank(e.level_id>>0),
                                time         : e.update_time===null?'暂无':e.update_time.substring(0,19),
                                ranking      : e.ranking===null?'暂无名次':e.ranking,
                                price        : e.price,
                                title        : e.title,
                                org          : e.org_name,
                                group        : e.groupName,
                                address      : _county[0] +' '+ _county[1] + ' ' + _county[2] + ' ' + e.address,
                                telNum       : e.link_phone,
                                linkMan      : e.link_name,
                                applyOrgName : e.apply_org_name,
                                orderID      : e.order_id
                            });
                        });
                        that.totalNum = (_data.total>>0);
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        this.$message.warning(_data.msg);
                    }
                },res=>{
                    that.isAjax = false;
                    this.$message.warning('网络开小差了');
                });
            },
            findIndex(id){
                let a='';
                this.allMemberInfo.forEach((e,i)=>{
                    if(e.id===id){
                        a = i;
                        return false;
                    }
                });
                return a;
            },
            formatDate(t){
                const _t = new Date(t);
                const f=(a)=>{
                    return a<10?'0'+a:a;
                };
                return _t.getFullYear()+'-'+f(_t.getMonth()+1)+'-'+f(_t.getDate())+' '+f(_t.getHours())+':'+f(_t.getMinutes());
            }
        },
        mounted(){
            const that = this;
            that.getData();
            if(window.location.href.includes('?pay')){
                that.$notify.success({
                    title:'支付成功',
                    message:'已成功申请证书',
                    offset:80
                });
            }
        }
    }
</script>
<style>
    #c_apply .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        border-top:2px solid #20a0ff;
    }
    #c_apply .el-tabs__item{
        box-sizing:content-box;
    }
    #c_apply .el-tabs.el-tabs--card{
        height:0;
    }
    #c_apply .orgDetail p{
        font-size:14px;
        margin-bottom:10px;
    }
    #c_apply .org-name{
        cursor:pointer;
        display:block;
        width:100%;
    }
    #c_apply .text-red{
        color:#ff4949;
        /*margin-left:16px;*/
    }
    #c_apply .text-red:hover{
        opacity:0.7;
    }
    #c_apply .text-green{
        color:#ff4949;

    }
    #c_apply .text-green:hover{
        opacity:0.7;
    }
    #c_apply .tip{
        color:#8492A6;
        font-size:12px;
        margin:4px 20px;
        text-align:right;
    }
    #c_apply .upload-img{
        width:100%;
        height:100%;
    }
    #c_apply .addMem{
        overflow: hidden;
        padding:20px;
    }
    #c_apply .addMem .head-title p{
        margin:10px 0;
    }
    #c_apply .add-area{
        margin-bottom:20px;
    }
    #c_apply .add-panel{
        padding:15px;
        cursor:pointer;
        border-bottom:1px solid #d3dce6;
    }
    #c_apply .add-panel:last-of-type{
        border-bottom:0;
    }
    #c_apply .add-panel .plus{
        display: inline-block;
        width:48px;
        height:48px;
        font-size:68px;
        line-height:38px;
        color:#fff;
        background: #999;
        margin-right:18px;
    }
    #c_apply .add-panel .mem-info{
        position: relative;
        top:-16px;
        color:#999;
    }
    #c_apply .mem-submit{

    }
    #c_apply .money{
        position: absolute;
        font-size:18px;
    }
    #c_apply .mem-dialog{
        min-width:500px;
    }
    #c_apply .text-blue{
        color:#20a0ff;
        font-size:16px;
    }
    #c_apply .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #c_apply .mem-dialog .mem-form .form-input.age{
        width:100px;
    }
    #c_apply .refresh{
        display:inline-block;
        margin:10px 0;
    }
    #c_apply .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #c_apply .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #c_apply .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #c_apply .el-icon-more {
        margin-left:14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius:0;
    }*/
    #c_apply .search-area .add-new{
        margin-left:20px;
    }
    #c_apply .search-area .search-group{
        width:80%;
    }
</style>


