import Login from '../page/login.vue'
import Main from '../page/main.vue'
import c404 from '../page/404.vue'
// 首页
import Home from '../page/Home/index.vue'
import More from '../page/Home/more.vue'
// 机构管理
import orgAdd from '../page/Organization/OrgAdd.vue'
import orgEdit from '../page/Organization/OrgAdd.vue'
import org from '../page/Organization/OrgList.vue'
// 会员管理
import MPlayer from '../page/Member/MPlayer.vue'
import addPlayer from '../page/Member/addPlayer.vue'
import importPlayer from '../page/Member/importPlayer.vue'
import transferPlayer from '../page/Member/changePlayer.vue'
import transferList from '../page/Member/changeList.vue'
import allotPlayer from '../page/Member/allotPlayer.vue'
import MCoach from '../page/Member/MCoach.vue'
import addCoach from '../page/Member/addCoach.vue'
import MJudge from '../page/Member/MJudge.vue'
import addJudge from '../page/Member/addJudge.vue'

// 赛事管理
import plan from '../page/Event/EPlan.vue'
import planApply from '../page/Event/EApply.vue'
import planAdd from '../page/Event/EAdd.vue'
import myEvent from '../page/Event/EMy.vue'
import applyFor from '../page/Event/EApplyFor.vue'
// 段级位管理（原证书管理）
import CWait from '../page/Credential/wait.vue'
import CRankingList from '../page/Credential/list.vue'
import CApply from '../page/Credential/apply.vue'
// import CSend from '../page/Credential/send.vue'
import CPrint from '../page/Credential/print.vue'
// 钱包管理
import WCreate from '../page/Wallet/WCreate.vue'
import WList from '../page/Wallet/WList.vue'
import WTransfer from '../page/Wallet/WTransfer.vue'
import WToYunbisai from '../page/Wallet/WToYunbisai.vue'
import WCash from '../page/Wallet/WCash.vue'
// 报表管理
import SCharts from '../page/Statistics/charts.vue'
// 权限管理
import authority from '../page/Setting/authority.vue'
import fee from '../page/Setting/fee.vue'
import bindYunbisai from '../page/Setting/bindYunbisai.vue'

export default [
    {
        path: '',component:Login
    },
    {
        path: '/login',component:Login
    },
    {
        path: '/print',component: CPrint
    },
    {
        path:'/main',component:Main,
        children:[
            {path:'404',component:c404},
            {path:'',component:Home},
            {path:'home',component:Home},
            {path:'more',component:More},

            { path: 'orgAdd',component:orgAdd},
            { path: 'orgEdit',component:orgEdit},
            { path: 'org', component: org},

            { path: 'player',component:MPlayer},
            { path: 'addPlayer',component:addPlayer},
            { path: 'importPlayer',component:importPlayer},
            { path: 'transferPlayer',component:transferPlayer},
            { path: 'allotPlayer',component:allotPlayer},
            { path: 'transferList',component:transferList},
            { path: 'judge',component:MJudge},
            { path: 'addJudge',component:addJudge},
            { path: 'coach',component:MCoach},
            { path: 'addCoach',component:addCoach},

            { path: 'plan',component:plan},
            { path: 'planAdd',component:planAdd},
            { path: 'planEdit',component:planAdd},
            { path: 'planApply',component:planApply},
            { path: 'eventApply',component:planAdd},
            { path: 'applyFor',component:applyFor},
            { path: 'myEvent',component:myEvent},

            { path: 'wait',component: CWait},
            { path: 'rankingList',component: CRankingList},
            { path: 'creList',component: CApply},
            // { path: 'send',component: CSend},

            { path: 'WCreate',component:WCreate},
            { path: 'WCash',component: WCash},
            { path: 'WTransfer',component: WTransfer},
            { path: 'WToYunbisai',component: WToYunbisai},
            { path: 'WList',component: WList},

            {path:'charts',component:SCharts},

            {path:'setting1',component:authority},
            {path:'setting2',component:fee},
            {path:'setting3',component:bindYunbisai}
        ]
    }
]


